@import url('https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat:wght@300&family=Prata&family=Rosario&display=swap');

.blend-container {
  background: linear-gradient(180deg, #e6d3b56f 0%, rgba(230, 211, 181, 0) 100%);
}

.blend-main {
  display: flex;
  flex-direction: column;
  padding: 0px 0px 0px 90px;
}

.blend-title {
  width: 293px;
  height: 130px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  letter-spacing: -0.01em;
  color: #000000;
  text-align: start;
}

.blend-line {
  width: 235px;
  height: 0px;
  margin-top: 20px;
  float: right;
  border: 1px solid #8b5632;
}

.blend-caption p {
  width: 186px;
  height: 33px;
  font-family: "Rosario";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  color: #724d48;
  text-align: start;
}

.blend-ingredients-images {
  display: flex;
  justify-content: space-evenly;

}

.oats-box {
  position: relative;
  width: 250px;
  height: 390px;
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.oatsimg {
  position: absolute;
  top: 0;
  width: 250px;
  height: 271px;
}

.blend-oats {
  position: absolute;
  width: 100%;
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  text-align: center;
  color: #000000;
  bottom: 40px;
}

.blend-ingredients-caption1 {
  position: absolute;
  width: 100%;
  height: 42px;
  font-family: 'Rosario';
  font-style: normal;
  font-weight: 400;
  font-size: 15px;
  text-align: center;
  color: #724D48;
  bottom: 20px;
}

.plus-icon {
  align-self: center;
  font-size: 40px;
}


@media only screen and (min-width: 767px) and (max-width: 899px) {
  .oats-box {
    scale: 0.8;
  }
}

@media only screen and (max-width: 767px) {
  .blend-ingredients-images {
    flex-wrap: wrap;
  }
  .blend-title{
    font-size: 55px;
  }
  .blend-line{
    margin-top: 0px;
  }
}