@import url("https://fonts.googleapis.com/css2?family=Prata&display=swap");

.certification-container {
  overflow: hidden;
  padding-bottom: 50px;
  /* background:#fdf1de; */
}

.certification-title {
  width: 80%;
  height: 100px;
  text-align: left;
  padding: 20px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 100.8%;
  letter-spacing: -0.01em;
  color: #000000;
  
}

.certification-title:hover {
  color: rgb(94, 35, 10);
}

.frame {
  width: auto;
  height: fit-content;
  background: #efe8de;
  padding: 2px 0px;
}

.vectors {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
  margin-top: 70px;
}

.not-tested {
  margin-top: 20px;
  width: 250px;
}

.vectors p {
  height: 27px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 27px;
  text-align: center;
  color: #8b5632;
  margin-bottom: 40px;
}

.cruely-free {
  margin-top: 20px;
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .vectors {
    scale: 0.9;
  }
}

@media only screen and (min-width: 900px) and (max-width: 1199px) {
  .vectors {
    scale: 0.8;
  }
}

@media only screen and (min-width: 767px) and (max-width: 900px) {
  .vectors {
    scale: 0.9;
  }

  .certification-title {
    padding: 0px;
    margin-left: 0px;
  }
}

@media only screen and (max-width: 767px) {
  .vectors {
    gap: 30px;
    flex-wrap: wrap;
  }

  .certification-title {
    font-size: 50px;
    padding: 0px;
    margin-left: 0px;
  }
}