@import url('https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap');

.fromthepress{
    padding-bottom: 100px;
    
}
.fromthepress-p {
    width: 80%;
    height: 65px;
    font-family: 'Prata';
    font-size: 64px;
    font-weight: 400;
    line-height: 65px;
    letter-spacing: -0.01em;
    text-align: left;
    padding-left: 100px;
}

.fromthepress-p:hover{
    color: rgb(94, 35, 10);
  }
  
.fromthepress .scrolling-wrapper {
    overflow-x: scroll;
    display: flex;
    margin-left: 100px;
    float: left;
    height: 330px;
    width: 93%;
    padding: 0;
}

#style::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
    background-color: #f5f5f500;
}

#style::-webkit-scrollbar {
    height: 7px;
    background-color: #f5f5f5fd;
}

#style::-webkit-scrollbar-thumb {

    background-color: #000000e1;
    /* border: 2px solid #ebdede; */
}


.card {
    height: 276px;
    width: 418px;
    margin-left: 20px;
    border-right: 1px solid #000000;
    padding: 0px 30px 0px 30px;
}

.card .title {
    height: 32px;
    width: 231px;
    font-family: 'Prata';
    font-size: 32px;
    font-weight: 400;
    line-height: 32px;
    letter-spacing: -0.01em;
    text-align: left;
}

.card .description {
    font-family: 'Roboto';
    font-size: 16px;
    font-weight: 400;
    line-height: 26px;
    text-align: left;
    height: 104px;
    width: 418px;
}

.logo-box {
    display: flex;
    flex-direction: row;
}

.logo-title {
    font-family: 'Roboto';
    font-size: 28px;
    font-weight: 400;
    line-height: 28px;
    margin: 0px 0px;
    letter-spacing: -0.01em;
    text-align: left;
    height: 28px;
    width: 221px;
}

.logo-caption {
    font-family: 'Roboto';
    font-size: 14px;
    font-weight: 400;
    text-align: left;
    margin: 0px 0px;
    height: 14px;
    width: 77px;
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
    .fromthepress .scrolling-wrapper {
        margin-left: 80px;
    }
    .fromthepress{
        margin-top: -170px;
    }

}
@media only screen and (min-width: 900px) and (max-width: 1199px) {
    .fromthepress .scrolling-wrapper {
        scale:0.9;
        margin-top: -80px;
    }
    
    .fromthepress{
        margin-top: -140px;
    }
}  
@media only screen and (min-width: 767px) and (max-width: 900px) {
    .fromthepress .scrolling-wrapper {
        scale:0.9;
        margin-top: -80px;
        margin-left: 80px;
    }
    .fromthepress{
        margin-top: -140px;
    }
}
@media only screen and (max-width: 767px) {
    .fromthepress .scrolling-wrapper {
        scale:0.8;
        width: 110%;
        margin-top: -80px;
        margin-left: 0px;
    }
    .fromthepress-p {
        font-size: 50px;
        margin-left: 50px;

    }
    .fromthepress .card{
        scale: 0.9;
    }
    .fromthepress{
        margin-top: -140px;
    }
}  