.OrderConfirmed-parent {
  display: grid;
  grid-template-columns: 1fr 0.7fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.OrderConfirmed-div1 {
  grid-area: 1 / 1 / 2 / 2;
}

.OrderConfirmed-div2 {
  grid-area: 1 / 2 / 2 / 3;
}
@media only screen and (min-width: 1200px) and (max-width: 1440px){
  .OrderConfirmed-parent {
    scale:0.8;
    margin-left: -130px;
    margin-top: -50px;
  }
}
@media only screen and (min-width: 900px) and (max-width: 1199px){
  .OrderConfirmed-parent {
    scale:0.6;
    margin-left: -230px;
    margin-top: -150px;
  }
  .OrderConfirmed-button{
    scale:0.8;
    width: 200px;
    height: 50px;
    margin-top: -120px;
    margin-right: 10px;
  }
}
@media only screen and (min-width: 768px) and (max-width: 899px){
  .OrderConfirmed-parent {
    scale:0.5;
    margin-left: -250px;
    margin-top: -200px;
  }
  .OrderConfirmed-button{
    scale:0.8;
    width: 200px;
    height: 50px;
    margin-top: -200px;
    margin-right: 10px;
  }
}
@media only screen and (min-width: 1710px) and (max-width: 1920px){
  .OrderConfirmed-parent {
    scale:0.9;
    margin-left: -90px;
    margin-top: 10px;
  }
  .OrderConfirmed-button{
    scale:0.8;
    width: 200px;
    height: 50px;
    margin-top: 50px;
    margin-right: 10px;
  }
  
}


