@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");

.faq-main {
  background: #f4ecdd;
  height: fit-content;
  padding-bottom: 100px;
}

.faqimg {
  position: relative;
  display: flex;
  flex-direction: column;
}

.faqimg img {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  overflow: hidden;
}

.faq-title {
  align-self: center;
  justify-self: center;
  display: flex;
  margin-top: -100px;
  position: absolute;
  width: 704px;
  height: 63px;
  top: 50%;
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 60px;
  line-height: 97.8%;
  text-align: center;
  letter-spacing: -0.01em;
  margin: 0px;
  color: #e3d9c6;
}

.faq-sub {
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
  height: 59px;
  padding: 50px 0px;
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 600;
  font-size: 60px;
  line-height: 97.8%;
  text-align: center;
  letter-spacing: -0.01em;
  color: #724d48;
}

.faqs {
  margin: 0px 100px;
}

.faq-line {
  width: 1200px;
  height: 0px;
  border: 2px solid #dfccae;
}

.hidden {
  display: none;
}

.faq-question {
  width: 1263px;
  height: 36px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: bold;
  font-size: 24px;
  line-height: 139.19%;
  color: #4d3e2f;
  padding: 10px 0px;
}

.faq-answer {
  width: 610px;
  height: 36px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 139.19%;
  color: #976e52;
  text-align: start;
  padding-bottom: 100px;
}
