@import url("https://fonts.googleapis.com/css2?family=Prata&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");

.routine-main {
  display: flex;
  flex-direction: row;
}

.routine-title p {
  width: 412px;
  height: 72px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 36px;
  line-height: 100.8%;
  color: #000000;
  text-align: start;
  padding-left: 90px;
}

.routine-line {
  width: 334px;
  height: 0px;
  border: 1px solid #8b5632;
  margin-top: 100px;
  margin-left: -40px;
}

.routine-image {
  position: relative;
  margin-top: 60px;
  overflow: hidden;
}

.routine-image-text img {
  width: 100%;
}

.routine-image .image-text {
  display: flex;
  flex-direction: column;
  position: absolute;
  right: 20px;
  bottom: 50px;
}

.routine-image-text .image-text1 {
  width: 370px;
  height: 105px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  color: #ffffff;
  margin: 0px;
}

.routine-image-text .image-text2 {
  width: 291px;
  height: 34px;
  text-align: start;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 105.8%;
  color: #ffffff;
  margin: 0px;
  padding-bottom: 30px;
}

.routine-button {
  padding: 25px 17px;
  gap: 10px;
  width: 390px;
  height: 67px;
  background: #3a383b;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 85.8%;
  color: #ffffff;
  border: none;
}

.routine-button:hover,
.routine-button:focus {
  box-shadow: inset 0 -3.25em 0 0 #2221219b;
  transform: translateY(-0.25em);
}


@media only screen and (min-width: 767px) and (max-width: 899px) {
  .image-text {
    scale: 0.5;
    top: 0px;
    margin-right: -100px;
    right: 50;
  }

  .routine-image {
    margin-top: 0px;
  }
}

@media only screen and  (max-width: 767px) {
  .routine-title p{
    padding-left: 30px;
  }
  .image-text {
    scale: 0.5;
    top: 0px;
    margin-right: -100px;
    right: 50;
  }

  .routine-image {
    margin-top: 0px;
  }
}



@media only screen and (min-width: 900px) and (max-width: 1199px) {
  .image-text {
    scale: 0.8;
    top: 20px;
    right: 0;
  }

  .routine-image {
    margin-top: 0px;
  }
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .routine-image .image-text {
    scale: 0.9;
    right: 100px;
    bottom: 80px;
  }
} 

@media only screen and (min-width: 1441px) and (max-width: 1709px){
  .routine-image .image-text {
    scale: 1;
    right: 160px;
    bottom: 130px;
  }
}

@media only screen and (min-width: 1710px) and (max-width: 1920px) {
  .routine-image .image-text {
    scale: 1.1;
    right: 200px;
    bottom: 180px;
  }
}
