.profile-parent {
  display: grid;
  grid-template-columns: 0.35fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.profile-div1 {
  grid-area: 1 / 1 / 2 / 2;
}
.profile-div2 {
  grid-area: 1 / 2 / 2 / 3;
}

.myprofile-main {
  position: relative;
}

.myprofile-pagetxt {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 6px;
  width: 44px;
  height: 22px;
  font-size: 16px;
  margin-top: 30px;
  margin-left: 140px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #9f9f9f;
}

.myprofile-pagetxt .myprofile-pagedes {
  width: 44px;
  height: 22px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: black;
}

.myprofile-sidebarmain {
  position: absolute;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 11px 0px;
  gap: 10px;
  position: absolute;
  width: 253px;
  height: 230px;
  left: 90px;
  top: 70px;
  background: #e3d9c6;
  border-radius: 6px;
}

.profile-tab,
.order-tab,
.wishlist-tab,
.logout-tab {
  display: flex;
  flex-direction: row;
  align-items: center;
  color: black;
  padding: 0px 0px 0px 15px;
  gap: 18px;
  width: 232px;
  height: 49px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 105.8%;
}

.profile-tab-active,
.order-tab-active,
.wishlist-tab-active,
.logout-tab-active {
  border-left: 4px solid #643731;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  align-items: center;
  padding: 0px 0px 0px 15px;
  gap: 18px;
  width: 232px;
  height: 49px;
  font-family: "Roboto";
  font-style: normal;
  font-size: 20px;
  line-height: 105.8%;
}




@media only screen and (min-width: 1201px) and (max-width: 1440px) {
  .profile-div2{
    margin-left: 70px;
  }
}

@media only screen and (min-width: 900px) and (max-width: 1200px) {
  .profile-parent {
  }
  .profile-div1{
    scale: 0.8;
    margin-left: 0px;
    margin-top: -100px;
  }
  .profile-div2{
    scale: 0.7;
    margin-top: -160px;
    margin-left: 0px;
  }
}
@media only screen and (min-width: 767px) and (max-width: 899px) {
  .profile-parent {
  }
  .profile-div1{
    scale: 0.8;
    margin-left: -70px;
    margin-top: -100px;
  }
  .profile-div2{
    scale: 0.7;
    margin-top: -160px;
    margin-left: 70px;
  }
}
