@import url("https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap");

.leaf {
  width: 176px;
  height: 264px;
}

.maindiv {
  overflow: hidden;
}
.storyimg {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: flex-end;
  overflow: hidden;
}

.oursto {
  position: absolute;
  width: 455px;
  height: 93px;

  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 96px;
  line-height: 96.5%;
  color: #fdf7eb;
  margin: 0px;
}

.exp p {
  width: 522px;
  height: 64px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 32px;
  line-height: 99.8%;
  letter-spacing: -0.01em;
  color: #724d48;
  margin: 0px;
}

.uncom p {
  width: 493px;
  height: 132px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 139.19%;
  color: #8f887a;
  margin: 0px;
}

.divour {
  justify-content: center;
  text-align: start;
  padding-left: 50px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 33px;
  width: 522px;
  height: 320px;
}

.ourstorydiv {
  display: flex;
  flex-direction: row;
  justify-content: center;
  padding: 30px 0px;
  margin-top: 20px;
}

@media only screen and (min-width: 900px) and (max-width: 1119px) {
  .ourstorydiv {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 30px 0px;
    margin-top: 20px;
    scale: 0.9;
  }
}
@media only screen and (min-width: 768px) and (max-width: 899px) {
  .ourstorydiv {
    display: flex;
    flex-direction: row;
    justify-content: center;
    padding: 30px 0px;
    margin-top: 20px;
    scale: 0.8;
  }
}
@media only screen and (min-width: 768px) and (max-width: 899px) {
  .oursto {
    position: absolute;
    width: 455px;
    height: 93px;

    font-family: "Prata";
    font-style: normal;
    font-weight: 400;
    font-size: 96px;
    line-height: 96.5%;
    color: #fdf7eb;
    margin: 0px;
    scale: 0.7;
  }
}
@media only screen and (min-width: 1710px) and (max-width: 1920px) {
  .storyimg {
    width: 100%;
  }
  .storyimg img {
    object-fit: cover;
    width: 100%;
  }
}
