@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");

.not {
  width: 140px;
  height: 40px;
  font-family: "prata";
  font-size: 40px;
  font-style: normal;
  font-weight: 450;
  font-size: 40px;
  line-height: 100.8%;
  letter-spacing: -0.01em;
  color: #000000;
}

.product-not-found-main {
  background-color: rgba(255, 254, 243, 1);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
  padding-top: 150px;
}

.txt {
  width: 276px;
  height: 44px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #724d48;
  flex: none;
  order: 0;
  flex-grow: 0;
}

.btn {
  width: 202px;
  height: 40px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  color: #3a383b;
  text-align: center;
  align-items: center;
  display: flex;
  border-color: #000000;
}

.explo button {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 25px 17px;
  width: 245px;
  height: 67px;
  border: 1px solid #000000;
  background-color: rgba(0, 255, 255, 0);
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  display: flex;
  align-items: center;
  text-align: center;
  color: #3a383b;
}

.stone {
  padding-bottom: 100px;
}
