.navbar {
    position: sticky;
    top: 0;
    z-index: 1;
}

.text {
    text-align: center;
    background: #2e2419;
    color: rgb(221, 195, 195);
    font-family: Roboto, sans-serif;
    padding: 7px;
    font-size: 14px;
    font-weight: 400;
    line-height: 16px;
}

.text h2 {
    font-size: 48px;
}

.nav {
    background: #FFF9ED;
    display: flex;
    justify-content: space-between;
}

.nav .logo {
    margin-top: 10px;
    margin-left: -400px;

}

.nav ul {
    display: flex;

}

.NavTabs {
    align-items: center;
    margin-left: 20px;
}

.NavTabs li {
    padding: 10px 20px 0px 0px;
    list-style: none
}

.navbar .icons {
    align-items: center;
    margin-right: 15px;
}

.navbar .icons {
    display: flex;
    align-items: center;
}

.navbar .icons li {
    padding: 10px 12px 0px 12px;
    list-style: none
}

.navbar ul li a {
    color: #3A383B;
    display: block;
    text-decoration: none;
    font-family: Montserrat, sans-serif;
    font-size: 14px;
    font-weight: 600;
    line-height: 12px;
    letter-spacing: 1px;
    text-align: left;
    text-transform: uppercase;
}

.menubaricon{
    display: none;
    position: absolute;
    left: 10px;
    top: 35px;
    padding: 10px 10px 10px 10px;
}

.sidenav {
    position: absolute;
    background: #371A0E;
    width: 287px;
    display: flex;
    flex-direction: column;
}

.menubar {
    display: flex;
    flex-direction: row;
    height: 65px;
    gap: 30px;
    font-size: 30px;
    font-family: 'Prata';
    font-style: normal;
    font-weight: 400;
    color: #FFFFFF;
    justify-content: flex-start;
    padding: 20px 0px 0px 20px;
}
.menubar img{
    width: 25px;
}

.menubarlist {
    display: flex;
    flex-direction: column;
    background: #F5F5F5;
    align-items: flex-start;
    list-style: none;
    text-align: left;
}

.menubarlist li {
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    padding: 10px 96px 10px 40px;
    height: 25px;
}

.menubarlist li a {
    color: #3A383B;
    text-decoration: none;
}


@media only screen and (min-width: 1200px) and (max-width: 1440px) {
    .navbar ul li a {
        font-size: 10px;
    }
}

@media only screen and (min-width: 900px) and (max-width: 1200px) {
    .navbar ul li a {
        font-size: 10px;
    }

    .nav .logo {
        margin-left: -110px;
    }
}

@media only screen and (min-width: 767px) and (max-width: 900px) {
    .navbar ul li a {
        visibility: hidden;
    }

    .menubaricon {
        display: block;
    }
}

@media only screen and (max-width: 767px) {
    .navbar ul li a {
        visibility: hidden;
    }
    .menubaricon {
        display: block;
    }
    .nav .logo{
        margin-left: -450px;
        
    }
}