@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Prata&family=Roboto&family=Rosario&display=swap");

.favourites-container {
  margin-top: -110px;
  /* background:#f7f5f2; */
}

.favourites-main {
  display: flex;
  flex-direction: row;
}

.favourites-title {
  margin-left: 90px;
  text-align: start;
  width: 425px;
  height: 130px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 100.8%;
  letter-spacing: -0.01em;
  color: #000000;
}

.favourites-title:hover {
  color: rgb(94, 35, 10);
}

.favourites-caption {
  text-align: start;
  margin-left: 90px;
  margin-top: -50px;
  width: 266px;
  height: 70px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 124.8%;
  color: #8b5632;
}

.favourites-line {
  margin-top: 180px;
  margin-left: -100px;
  width: 328px;
  height: 0px;
  border: 1px solid #8b5632;
}

.favourites-hairoil-images {
  display: flex;
  flex-direction: row;
  margin-left: -50px;
  justify-content: center;
}

.favourites-card {
  margin-right: 20px;
}

.favourites-card img:hover {
  scale: 1.03;
}

.favourites-image-title {
  width: 221px;
  height: 26px;
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 108.8%;
  color: #000000;
}

.favourites-image-caption {
  text-align: start;
  width: 379px;
  height: 60px;
  font-family: "Rosario";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  color: #724d48;
}

.favourites-price {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 10px 20px;
  gap: 16px;
  background: #3a383b;
  border-radius: 42px;
  width: 62px;
  height: 18px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  color: #ffffff;
  justify-content: center;
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .favourites-hairoil-images {
    scale: 0.8;
    margin-top: -70px;
  }

}

@media only screen and (min-width: 900px) and (max-width: 1199px) {
  .favourites-hairoil-images {
    scale: 0.6;
    margin-left: 20px;
    margin-top: -130px;
  }
}
@media only screen and (min-width: 767px) and (max-width: 899px) {
  .favourites-hairoil-images {
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

  .favourites-image-title {
    width: 221px;
    height: 26px;
    font-family: "Baskerville";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
  }
}

@media only screen and (min-width: 1710px) and (max-width: 1920px) {
  .favourites-hairoil-images {
    margin-left: 70px;
    gap: 50px;
  }
  .favourites-image-title {
    text-align: start;
  }
  .favourites-price {
    margin-top: -20px;
    height: 24px;
    margin-bottom: 50px;
  }
  .favourites-image-caption {
    margin-top: -10px;
  }
}
@media only screen and (max-width: 767px) {
  .favourites-hairoil-images {
    scale: 0.9;
    margin-top: -120px;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }

}
