@import url("https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap");

.whatcare {
  display: flex;
  flex-direction: column;
  margin-left: 100px;
}

.whatcare p {
  font-family: "Prata";
  font-size: 64px;
  font-weight: 400;
  line-height: 65px;
  text-align: left;
  height: 100px;
  width: 590px;
}

.whatcare:hover {
  color: rgb(94, 35, 10);
}

.whatcare .caption {
  font-family: "Roboto";
  font-size: 30px;
  font-weight: 400;
  line-height: 30px;
  letter-spacing: 0px;
  text-align: left;
  height: 84px;
  width: 380px;
  margin-top: 0px;
}

.whatcare-line {
  display: flex;
  flex-direction: row;
}

.line {
  width: 328px;
  height: 0px;
  margin-top: 180px;
  margin-left: -20px;
  border: 1px solid #8b5632;
}

.squareimage .parent {
  display: grid;
  grid-template-columns: repeat(9, 1fr);
  grid-template-rows: repeat(5, 1fr);
  grid-column-gap: 15px;
  grid-row-gap: 15px;
  margin-top: -80px;
  height: auto;
  scale: 0.85;
}

.image1 {
  grid-area: 1 / 2 / 3 / 4;
  height: 280px;
  border-radius: 20px;
  overflow: hidden;
  background-color: #D1D6D9;
}

.image1 p {
  position: absolute;
  font-family: "Prata";
  font-size: 64px;
  color: aliceblue;
  font-weight: 400;
  line-height: 68px;
  margin: 10px 30px;
  letter-spacing: 0em;
  text-align: left;
  height: 68px;
  width: 162px;
}

.image1 img {
  width: 100%;
  object-fit: contain;
}

