@import url('https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap');

.home1 {
    background: #eedec4;
}

.homeparent {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
    height: 1050px;
}

.homeparent .div1 {
    grid-area: 1 / 1 / 2 / 3;
    width: auto;
    height: 350px;
    padding: 7px 77px 266px 63px;
    background-color: rgba(55, 26, 14, 1);
    overflow: hidden;
    background-image: url("../../images/homeimage.svg");
    background-repeat: no-repeat;
    background-size: cover;
}


.div1 .healyourskin p {
    font-family: 'Prata', serif;
    font-size: 95px;
    color: rgba(227, 217, 198, 1);
    line-height: 0px;
    text-align: left;
}

.healyourskin .your {
    margin-top: -20px;
}

.caption p {
    font-family: 'Roboto';
    font-size: 24px;
    font-weight: 300;
    line-height: 5px;
    color: rgba(151, 110, 82, 1);
    text-align: left;
}

.cap-image {
    display: flex;
    margin-top: -20px;
}

.shopall-btn {
    display: flex;
    border-radius: 51px;
    font-family: 'Roboto';
    color: #ffffff;
    font-size: 30px;
    background: #0d0e0f00;
    padding: 10px 30px 10px 30px;
    border: solid #ffffff 2px;
    text-decoration: none;
}

.shopall-btn:hover,
.shopall-btn:focus {
    box-shadow: inset 6.5em 0 0 0 #e8ddd899;
    color: #000000;
    border: solid #ededed 2px;
    padding: 13px 33px 13px 33px;
    transition: cubic-bezier(0.075, 0.82, 0.165, 1);

}

.div3 {
    grid-area: 2 / 1 / 3 / 3;
    justify-content: center;
    height: 400px;
}

.box3 {
    margin-top: 30px;
}

.caption2 p {
    font-family: 'Prata';
    font-size: 32px;
    font-weight: 400;
    line-height: 2px;
    letter-spacing: -0.01em;
    text-align: center;
    color: rgba(139, 86, 50, 1);
}

.caption3 p {
    margin-top: 70px;
    font-family: 'Roboto';
    font-size: 16px;
    font-weight: 400;
    line-height: 20px;
    letter-spacing: 0px;
    text-align: center;
    color: rgb(100, 67, 62);
}

.chatvector {
    position: fixed;
    bottom: 30px;
    right: 50px;
    z-index: 1;
}

.div4 {
    grid-area: 3 / 1 / 4 / 3;
    width: 100%;
    height: 60px;
    margin-top: -25px;
    position: relative;
    overflow: hidden;
    background-color: rgba(55, 26, 14, 1);
}

.div4 p {
    left: 0%;
    top: 0%;
    position: absolute;
    color: wheat;
    padding: 20px 0px;
    width: 100%;
    margin-top: -5px;
    font-size: 17px;
    height: 10px;
    width: inherit;
    animation: primary 18s linear infinite;
    white-space: nowrap;
}

@keyframes primary {
    0% {
        transform: translate3d(0, 0, 0);
    }

    100% {
        transform: translate3d(-1500px, 0, 0);
    }
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
    .chatvector {
        bottom: 30px;
        right: 50px;
        scale: 0.8;
    }

    .homeparent .div1 {
        padding: 7px 77px 266px 100px;

    }
}
@media only screen and (min-width: 900px) and (max-width: 1199px) {
    .chatvector {
        bottom: 20px;
        right: 30px;
        scale: 0.8;
    }
    .homeparent .div1 {
        background-position: -150px 0px ;
    }
}

@media only screen and (min-width: 767px) and (max-width: 899px) {
    .chatvector {
        bottom: 20px;
        right: 30px;
        scale: 0.7;
    }

    .homeparent .div1 {
        padding: 0px 77px 266px 10px;
    }

    .shopall-btn {
        font-size: 15px;
    }

    .caption p {
        font-family: 'Roboto';
        font-size: 20px;
        font-weight: 300;
        line-height: 5px;
        color: rgba(151, 110, 82, 1);
        text-align: left;
    }

    .cap-image {
        display: flex;
        margin-top: -20px;
    }

    .homeimage img {
        /* scale: 1; */
        margin: -10px 0px 0px -200px;
    }

}
@media only screen and (max-width: 767px) {
    .homeparent .div1 {
        background-position: -300px 0px;
    }
    .chatvector {
        bottom: 20px;
        right: 30px;
        scale: 0.7;
    }

}

