@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");

.grid-section {
  padding: 20px 30px;
}

.grid-dropdown {
  display: flex;
  justify-content: end;
  margin-right: 40px;
 margin-bottom: 40px; 
  margin-top: -90px; 
}

.grid-dropdown select {
  padding: 4px 10px;
  width: 200px;
  height: 30px;
  background: #f5e8d6;
  border-radius: 34px;
  overflow: hidden;
  white-space: pre-wrap;
  text-overflow: clip;
}

.grid-dropdown option {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  color: #724d48;
  width: 200px;
  overflow: hidden;
  text-overflow: ellipsis;
}

.productgrid-container {
  max-width: 1200px;
  margin: auto;
  /* margin-top: 400px; */
}

.grid-row {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(240px, 190px));
  grid-gap: 15px 15px;
  justify-content: center;
}

.grid-row .grid-productcard {
  transition: filter 0.5s ease;
  display: flex;
  cursor: pointer;
  font-family: sans-serif;
  flex-direction: column-reverse;
  padding-bottom: 5px;
  margin-bottom: 10px;
}

.grid-productname-and-price {
  display: flex;
  justify-content: space-between;
  width: 235px;
  height: auto;
  flex-direction: row;
  padding: 1px 4px 1px 4px;
}

.grid-description {
  display: flex;
  flex-direction: column;
  height: 40px;
  padding: 15px 4px 15px 2px;
}

.grid-productname {
  width: 200px;
  height: 17px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 108.8%;
  color: #000000;
  text-align: start;
  margin: 0px;
  padding-bottom: 20px;
}

.grid-productprice {
  width: 39px;
  height: 18px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.1px;
  color: #474740;
  margin: 0px;
}

.grid-productrating {
  width: 49px;
  height: 18px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.1px;
  color: #8f887a;
  margin: 0px;
  padding: 1px 4px 1px 5px;
  text-align: start;
}

.grid-button {
  display: flex;
  justify-content: center;
  margin-top: 5px;
  padding: 10px;
}

.grid-button button {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 25px 17px;
  gap: 10px;
  position: absolute;
  width: 215px;
  height: 67px;
  border: 1px solid #000000;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  color: #3a383b;
}

.grid-image {
  position: relative;
  float: right;
  overflow: hidden;
  object-fit: cover;
  

}



.grid-productlistcart {
  position: absolute;
  right: 15px;
  bottom: 15px;
}
@media only screen and (min-width: 1461px) and (max-width: 1709px){
  .grid-row {
    
    gap: 60px;
    width: 100%;
   
  }
    .grid-section {
      padding: inherit;
      margin-top: -100px;
      
  }

.grid-dropdown{
  margin-right: 100px;

}

} 
@media only screen and (min-width: 1710px) and (max-width: 1788px){
  .grid-row {
    margin-left: -60px;
    gap: 70px;
    width: 100%;
    scale: 1.1;
  }
  .productgrid-container {
    max-width: 1200px;
    /* margin: auto; */
    margin-top: 120px;
}

.grid-dropdown{
  margin-right: 110px;
  
 
} 
}

  

@media only screen and (min-width: 1788px) and (max-width: 1920px){
  .grid-row {
    margin-left: -60px;
    gap: 70px;
    width: 80%;
    margin-left: 120;
   
    /* scale: 1.1; */
  }
  .grid-dropdown{
    margin-right: 300px;
    
   
  } 
  




} 
 

 
