@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");
@import url("https://fonts.googleapis.com/css2?family=Prata&display=swap");

.pick-main {
  padding-bottom: 50px;
  width: 100%;
  background-image: url(../../images/routineimg.svg);
  background-repeat: no-repeat;
  background-size: cover;
  overflow: hidden;
}
.picktxt{
  width: 100%;
  font-family: 'Prata';
  font-style: normal;
  font-weight: 400;
  font-size: 96px;
  text-align: center;
  color: #e3d9c6;
  margin-top: 50px;
}
.nourish{
  font-family: 'Prata';
  font-style: normal;
  font-weight: 300;
  font-size: 43px;
  line-height: 97.8%;
  text-align: center;
  letter-spacing: -0.01em;
  color: #e3d9c6;
  margin-top: 50px;
}
.pick-frame-main{
  display: flex;
  justify-content: center;
  margin-top: 60px;
}
.pickframe{
  width: 80%;
  padding: 100px 0px;
  height: fit-content;
  background-color: #e3d9c6;
}

.pick-quesion{
  text-align: center;
  font-family: 'Prata';
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  color: #4d3e2f;
}
.pickbutton {
  font-family: 'Roboto';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  border: none;
  color: #ffffff;
  padding: 25px 17px;
  gap: 10px;
  width: 409px;
  height: 67px;
  background: #3a383b;
}