.photoimage-container {
  height: 700px;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-top: -130px;
}

.photoimage-left {
  display: flex;
  height: 50vh;
}

.photoimage-left_1 {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

.img_wrap {
  border: 1px solid #eee;
  cursor: pointer;
}

.img_wrap img {
  width: 90px;
  height: 150px;
  object-fit: contain;
}

.photoimage-left_2 {
  width: 587px;
  height: 650px;
}

.photoimage-left_2 img {
  width: 487px;
  height: 629px;
  object-fit: contain;
}

.active {
  border: 2px solid #e77600;
}

@media only screen and (min-width: 900px) and (max-width: 1199px) {
  .photoimage-container {
    margin-top: -200px;
  }
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .photoimage-container {
    scale: 0.9;
    margin-left: -70px;
    margin-top: -100px;
  }

  .photoimage-left_2 {
    width: 530px;
    height: 650px;
  }
}

@media only screen and (min-width: 1710px) and (max-width: 1920px) {
  .photoimage-container {
    scale: 1.3;
    margin-left: 80px;
  }
}
