@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");

.footer-container {
  width: 100%;
  display: flex;
  margin: 10px 0px 0px 0px;
}

.footer-parent {
  display: grid;
  width: 100%;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 0.15fr 1fr 0.15fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  background: linear-gradient(180deg,
      rgba(230, 211, 181, 0.17) 0%,
      #e6d3b5 97.92%);
}

.footer-backgroundimage {
  background-image: url(../images/footerimage1.svg);
  background-repeat: no-repeat;
  width: 450px;
  height: 495px;
}

.footer-div1 {
  grid-area: 1 / 1 / 2 / 3;
  display: flex;
  justify-content: space-around;
  align-items: center;
  padding: 14px 20px;
  height: 20px;
  background: #3a383b;
}

.footer-div1 p {
  height: 14px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 85.8%;
  color: #ffffff;
  padding: 0px 5px;
}


.footer-div1 p:hover{
  scale: 0.92;
}


.footer-div2 {
  grid-area: 2 / 1 / 3 / 2;
  text-align: start;
  display: flex;
  height: 330px;
  grid-gap: 20px;
  padding: 30px;
  justify-content: space-between;
  border-right: 1px solid #976e52;
  position: relative;
}

.footer-div2 a {
  display: flex;
  flex-direction: column;
  padding-left: 2rem;
  text-decoration: none;
  color: #724d48;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 28px;
}

.footer-div2 a:hover{
  margin-left: -5px;
}

.footer-div2 h4 {
  display: flex;
  flex-direction: column;
  padding-left: 2rem;
  text-decoration: none;
  color: #724d48;
  font-family: "Baskerville";
}

.footer-footercomponents .footercomponenttitle {
  height: 14px;
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 85.8%;
}

.footer-iconspack {
  height: 40px;
  display: flex;
  flex-direction: row;
  position: absolute;
  bottom: 0px;
  right: 20px;
}

.footer-iconspack p {
  width: 176px;
  height: 14px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 85.8%;
  color: #976d50;
  margin-top: 10px;
}

.footer-icons {
  display: flex;
  flex-direction: row;
}

.footer-icons img {
  width: 33px;
  height: 33px;
  padding-left: 8px;
}

.footer-div3 {
  grid-area: 2 / 2 / 3 / 3;
  text-align: start;
  padding: 0px 100px;
  width: fit-content;
  height: auto;
}

.footer-textinput {
  background-color: #a99dae00;
  width: 404px;
  height: 40px;
  border-top: none;
  border-right: none;
  border-color: #724d48;
  border-left: none;
  font-family: "Baskerville";
  font-size: 18px;
}

.footer-textinput::placeholder {
  color: #724d48;
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  border: none;
}

.footer-div3 .divbox {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding: 20px;
  padding-bottom: 10px;
  gap: 30px;
  width: 297px;
  height: 44px;
}

.divbox p {
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 111.8%;
  color: #724d48;
}

.footer-subscribe {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 25px 17px;
  gap: 10px;
  width: 409px;
  height: 55px;
  background: #3a383b;
  color: #ffffff;
  margin-top: 20px;
  border: none;
}

.footer-div4 {
  grid-area: 3 / 1 / 4 / 3;
  border: 1px solid #976e52;
  height: 50px;
  display: flex;
  justify-content: center;
}

.footer-div4 p {
  width: 509px;
  height: 14px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  color: #4d3e2f;
}

@media only screen and (min-width: 900px) and (max-width: 1100px) {
  .footer-div2 {
    padding-left: 0;
  }
  .footer-div3{
    padding: 0px 0px 0px 40px;
  }
  .footer-div1 p {
    font-size: 12px;
  }
  .footer-textinput{
    width: 300px;
  }
  .footer-subscribe{
    width: 300px;
  }
}

@media only screen and (min-width: 767px) and (max-width: 900px) {
  .footer-parent {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 0.15fr repeat(2, 1fr) 0.15fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
  }

  .footer-div1 {
    grid-area: 1 / 1 / 2 / 3;
  }

  .footer-div1 p {
    font-size: 12px;
  }

  .footer-div3 {
    grid-area: 2 / 1 / 3 / 2;
  }

  .footer-div2 {
    grid-area: 3 / 1 / 4 / 2;
  }

  .footer-div4 {
    grid-area: 4 / 1 / 5 / 2;
  }

}
@media only screen and (max-width: 767px) {
  .footer-parent {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 0.15fr repeat(2, 1fr) 0.15fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
  }

  .footer-div1 {
    grid-area: 1 / 1 / 2 / 3;
  }

  .footer-div1 p {
    font-size: 12px;
  }

  .footer-div3 {
    grid-area: 2 / 1 / 3 / 2;
  padding: 0px 50px;

  }

  .footer-div2 {
    grid-area: 3 / 1 / 4 / 2;
  }

  .footer-div4 {
    grid-area: 4 / 1 / 5 / 2;
  }

}