@import url('https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap');

.home2 {
    display: flex;
    flex-direction: row;
    height: 650px;

}

.caption1 {
    display: flex;
    font-family: 'Prata';
    font-size: 60px;
    font-weight: 400;
    line-height: 63px;
    letter-spacing: 0em;
    text-align: left;
    height: 126px;
    width: 400px;
    margin-left: 112px;
}
.caption1:hover{
    color: rgb(94, 35, 10);
}


.subcaption1 {
    font-family: 'Roboto';
    font-size: 16px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0px;
    text-align: left;
    height: 66px;
    width: 199px;
    margin-left: 123px;
    margin-top: -50px;
}

.b-images {
    display: flex;
    margin-top: -100px;
}

.butterflyimage {
    transform: rotate(-10.56deg);
    position: absolute;
}

.butterflyimage1 {
    transform: rotate(19.44deg);
    position: absolute;
    margin-left: 200px;
    margin-top: -100px;
    scale: 0.5;
}

.butterflyimage2 {
    position: absolute;
    transform: rotate(-17.63deg);
    margin-left: 400px;
    margin-top: -100px;
    scale: 0.3;
}

.rightpanel {
    position: relative;
    margin-left: 100px;
}

.bottle {
    margin-top: 00px;
    position: absolute;
    margin-left: 200px;
}

.roundtext {
    position: absolute;
    margin-top: -30px;
    margin-left: 30px;
    animation: spin 7000ms linear infinite;

}
@keyframes spin { 
    from { 
        transform: rotate(0deg); 
    } to { 
        transform: rotate(360deg); 
    }
}

.boxborder {
    height: 173px;
    width: 370px;
    position: absolute;
    border: 2px dotted #000000;
    border-left: none;
    border-bottom: none;
    margin-top: 100px;
    margin-left: 280px;
}

.oats {
    margin-top: 150px;
    position: absolute;
    margin-left: 560px;
    font-family: 'Roboto';
}

.oats p {
    margin-top: -10px;
}

.oats button {
    width: 200px;
    font-family: 'Roboto';
    margin-top: 20px;
    background-color: #724D48;
    border-radius: 2px;
    border: none;
    color: #ffffff;
    font-size: 15px;
    padding: 10px 20px 10px 20px;
}
.oats button:hover,
.oats button:focus {
  box-shadow: inset 0 -3.25em 0 0 #2221219b;
  transform: translateY(-0.25em);
}

.subcaption2 {
    font-family: 'Roboto';
    font-size: 16px;
    position: absolute;
    margin-left: 900px;
    font-weight: 400;
    line-height: 22px;
    letter-spacing: 0px;
    text-align: right;
    height: 44px;
    width: 241px;
}


@media only screen and (min-width: 730px) and (max-width: 899px) {
    .rightpanel {
        scale: 0.7;
        margin-top: 100px;
        margin-left: -350px;

    }

    .subcaption2 {
        right: 100px;
        margin: 40px 0px 0px 0px;
    }
}

@media only screen and (min-width: 900px) and (max-width: 1199px) {
    
   
    .caption1 {
        font-size: 52px;
       
    }
    .subcaption1 {
        text-align: start;
        font-size: 14px;
        width: 190px
    }

    .rightpanel {
        scale: 0.6;
        margin-top: 80px;
        margin-left: -140px;

    }

    .subcaption2 {
        right: 120px;
        margin-top: 180px;
        font-size: 14px;
    }
    .roundtext {
        margin-top: -60px; 
        margin-left: 55px;
    }
    
    .oats button {
        height: 56px;
        width: 220px;
        font-size: 16px;
    }
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
    .rightpanel {
        margin-top: 60px;
        margin-left: 60px;

        scale: 0.8;
    }
    .subcaption2 {

        margin-left: 870px;
        margin-top: 100px;
    }
    .roundtext {

        margin-top: -60px; 
        margin-left: 55px;
    
    }
    .oats {

        margin-left: 540px;
    }
    .oats button {
        height: 54px;
        width: 220px;
        font-size: 16px;
    }
}

@media only screen and (min-width: 1441px) and (max-width: 1709px){
    .rightpanel{
        margin-top: 100px;
        margin-left: 20px;
    }
    .subcaption2 {
       
        margin-left: 940px;
        margin-top: 100px;
    }
    .oats {

        margin-left: 540px;
    }
    .oats {

        margin-left: 530px;
    }


    .oats button {
        height: 58px;
        width: 220px;
        font-size: 16px;
    }
}

@media only screen and (min-width: 1710px) and (max-width: 1920px) {
    .rightpanel{
        margin-top: 100px;
        margin-left: 270px;
    }
    .subcaption2 {
        
        margin-left: 1190px;
        margin-top: 100px;
     
    }

    .oats {
       
        margin-left: 540px;
       
    }

    .oats button {
        height: 54px;
        width: 220px;
