    }
}
@media only screen and (max-width: 767px) {
    .rightpanel{
        scale: 0.7;
        margin-top: 170px;
        margin-left: -500px;
    }
    .b-images{
        scale: 0.8;
        margin-left: -100px;
    }
    .subcaption2{
        font-size: 14px;
        margin: 260px 0px 0px 200px;
    }
