.home2mobile {
  padding-left: 20px;
  height: 414px;
}

.home2mobile-title {
  width: 100%;
}

.home2mobile-caption1 {
  width: 232px;
  height: 77px;
  text-align: start;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 34px;
  letter-spacing: -0.01em;
  color: #000000;
  margin: 0px;
}

.home2mobile-subcaption1 {
  width: 216px;
  height: 48px;
  text-align: start;
  font-family: "Rosario";
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 118.8%;
  display: flex;
  align-items: flex-end;
  letter-spacing: -0.01em;
  color: #976e52;
  margin: 0px;
}

.home2mobile-imagebox {
  position: relative;
  width: 300px;
  height: 200px;
}

.home2mobile-roundtext {
  width: 230px;
  position: absolute;
  margin-left: 45px;
  margin-top: -50px;
}

.home2mobile-oatsimage {
  position: absolute;
  width: 100px;
  height: 130px;
  margin-left: -25px;
  margin-top: 60px;
}

.home2mobile-dottedline {
  position: absolute;
  width: 60px;
  height: 0px;
  border: 1px dashed #8b5632;
  margin-left: 220px;
  margin-top: 120px;
}

.home2mobile-bottle {
  position: absolute;
  height: 200px;
  margin-left: 120px;
}

.home2mobile-butterfly-images {
  position: absolute;
  top: 145px;
  left: 225px;
}

.home2mobile-butterflyimage1 {
  width: 132.56px;
  height: 145.17px;
  transform: rotate(-10.56deg);
}

.home2mobile-butterflyimage2 {
  position: absolute;
  top: 20px;
  left: 100px;
  width: 38.83px;
  height: 42.52px;
  transform: rotate(19.44deg);
}

.home2mobile-button {
  margin-top: 30px;
  margin-left: 135px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 150px;
  height: 48px;
  background: #371a0e;
  border: 1px solid #fffef3;
  border-radius: 45px;
}

.home2mobile-button p {
  width: 92px;
  height: 24px;
  font-family: "Rosario";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  color: #fffef3;
}
