@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Prata&family=Rosario&display=swap");

.concernscontainer {
  margin-top: 0px;
  padding-bottom: 100px;
}

.concerns-main {
  display: flex;
  flex-direction: row;
}

.concerns-title p {
  width: 393px;
  height: 130px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 100.8%;
  letter-spacing: -0.01em;
  color: #000000;
  text-align: start;
  padding-left: 90px;
}

.concerns-title p:hover {
  color: rgb(94, 35, 10);
}

.concerns-line {
  width: 290px;
  height: 0px;
  margin-left: -40px;
  margin-top: 180px;
  border: 1px solid #8b5632;
  margin-left: -100px;
}

.concerns-caption p {
  width: 276px;
  height: 33px;
  font-family: "Rosario";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 139.19%;
  color: #724d48;
  text-align: start;
  padding-left: 90px;
  margin-left: -670px;
  margin-top: 190px;
}

.concerns-images {
  display: flex;
  justify-content: space-evenly;
  padding-top: 100px;
}

.concerns-images p{
  display: flex;
  justify-content: space-evenly;
  height: 25px;
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 108.8%;
  color: #000000;
  margin-left: -20px;
}

@media only screen and (max-width: 767px) {
.concerns{
  scale: 0.8;
  margin-top: -100px;
}
.concerns-images{
  gap: 30px;
  flex-wrap: wrap;
}
.concerns-name{
  gap: 60px;
}
.concerns-caption p{
  margin-left: -550px;
}
}
