.home1mobile {
  overflow: hidden;
}

.home1mobile-image {
  width: 100%;
  overflow: hidden;
}

.home1mobile-texts {
  background: #371a0e;
  padding-left: 20px;
}

.home1mobile-heading {
  width: 232px;
  height: 189px;
  text-align: start;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 56px;
  margin: 0px;
  padding-top: 20px;
  color: #e3d9c6;
}

.home1mobile-caption {
  width: 150px;
  height: 48px;
  font-family: "Rosario";
  font-style: normal;
  font-weight: 300;
  font-size: 20px;
  line-height: 118.8%;
  display: flex;
  align-items: flex-end;
  letter-spacing: -0.01em;
  margin: 0px;
  color: #976e52;
  text-align: start;
}

.home1mobile-button {
  margin: 30px 0px;
  width: 170px;
  height: 50px;
  background: #371a0e;
  border: 1px solid #e3d9c6;
  display: flex;
  justify-content: center;
  align-items: center;
  border-radius: 25px;
}

.home1mobile-button p {
  width: 71px;
  height: 30px;
  font-family: "Rosario";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 24px;
  color: #fffef3;
}

.home1mobile-scroll {
  margin-left: 370px;
  width: 179px;
  height: 0px;
}

.home1mobile-line {
  width: 179px;
  height: 0px;
  margin-top: -140px;
  border: 1px solid rgba(227, 217, 198, 0.29);
  transform: rotate(-90deg);
}

.home1mobile-scroll p {
  margin-top: -100px;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 8px;
  line-height: 85.8%;
  letter-spacing: 0.385em;
  color: #e3d9c6;
  transform: rotate(-90deg);
}

.home1mobile .home1mobile-background {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  margin-top: 100px;
  height: 300px;
  background-image: linear-gradient(
      180deg,
      #e1c393 0%,
      rgba(230, 211, 181, 0) 113.98%
    ),
    url("../../../assets/images/home1mobilebgt.svg");
  mix-blend-mode: multiply;
  background-repeat: no-repeat;
}

.home1mobile-background p {
  width: 313px;
  height: 52px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 131.8%;
  text-align: center;
  letter-spacing: -0.01em;
  color: #724d48;
}
