.productdetail-parent {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.productdetail-div1 {
  grid-area: 1 / 1 / 4 / 2;
  padding-left: 150px;
}

.productdetail-div2 {
  grid-area: 1 / 2 / 4 / 3;
}

.productdetail-page-path {
  text-align: start;
  margin: 0px 150px;
}

.productdetail-back {
  width: 276px;
  height: 22px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #9f9f9f;
}

.productdetail-path {
  height: 22px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #9f9f9f;
}

@media only screen and (min-width: 900px) and (max-width: 1199px) {
  .productdetail-parent {
    scale: 0.65;
    margin-left: -200px;
    margin-top: -100px;
  }
  .productdetail-container {
    width: 100%;
  }
}

@media only screen and (min-width: 1710px) and (max-width: 1920px) {
  .productdetail-parent {
    scale: 1.01;
    margin-left: 10px;
    margin-top: 200px;
  }
}
@media only screen and (min-width: 768px) and (max-width: 899px) {
  .productdetail-parent{
    grid-template-rows: repeat(2, 1fr);
  }
  .productdetail-div1{ grid-area: 1 / 1 / 2 / 2; }
.productdetail-div2 { grid-area: 2 / 1 / 3 / 2; }

}