.m-certifications {
  padding: 20px 0px;
}

.m-certifications-title {
  width: 239px;
  height: 77px;
  text-align: start;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 34px;
  padding-left: 20px;
  letter-spacing: -0.01em;
  color: #000000;
  margin: 0px;
}

.m-certifications-caption {
  width: 272px;
  height: 28px;
  text-align: start;
  font-family: "Rosario";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 139.19%;
  color: #724d48;
  margin: 0px;
  padding-left: 20px;
}

.m-certifications-box {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 100%;
  height: 350px;
  padding-top: 10px;
  background: #efe8de;
}

.m-certifications-item {
  scale: 0.95;
  display: flex;
  flex-direction: column;
  margin-bottom: 10px;
  justify-content: center;
  align-items: center;
}

.m-certifications-item p {
  width: 136px;
  height: 28px;
  font-family: "Rosario";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 139.19%;
  color: #724d48;
  align-items: center;
}
