@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");

.mywishlisttxt {
  margin-bottom: 40px;
  margin-left: 20px;
  width: 489px;
  height: 68px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 105.8%;
  color: #000000;
}

.grid-section {
  padding: 35px 30px;
}

.wishlist-container {
  margin-left: -20px;
  text-align: left;
}

.wishlist-grid-row {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(300px, 190px));
  grid-gap: 15px 15px;
  justify-content: center;
}

.wishlist-grid-row .wishlist-productcard {
  transition: filter 0.5s ease;
  display: flex;
  cursor: pointer;
  font-family: sans-serif;
  flex-direction: column-reverse;
}

.wishlist-productname-and-price {
  display: flex;
  justify-content: space-between;
  width: 235px;
  height: auto;
  flex-direction: row;
  padding: 1px 4px 1px 4px;
}

.wishlist-description {
  display: flex;
  flex-direction: column;
  height: 40px;
  padding: 15px 4px 15px 2px;
}

.wishlist-productname {
  width: 221px;
  height: 17px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 108.8%;
  color: #000000;
  text-align: start;
  margin: 0px;
}

.wishlist-productprice {
  width: 39px;
  height: 18px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.1px;
  color: #474740;
  margin: 0px;
}

.wishlist-productrating {
  width: 49px;
  height: 18px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  letter-spacing: 0.1px;
  color: #8f887a;
  margin: 0px;
  padding: 1px 4px 1px 5px;
}

.wishlist-button {
  display: flex;
  justify-content: center;
  margin-top: 5px;
  padding: 10px;
}

.wishlist-button button {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  padding: 25px 17px;
  gap: 10px;
  position: absolute;
  width: 215px;
  height: 67px;
  border: 1px solid #000000;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  color: #3a383b;
}

.wishlist-image {
  position: relative;
  float: right;
  overflow: hidden;
}

.wishlist-productlistcart {
  position: absolute;
  right: 65px;
  bottom: 25px;
}
