@import url("https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap");

.shop-all {
  margin-top: 50px;
}

.shop-all p {
  width: 261px;
  height: 40px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 100.8%;
  text-align: start;
  margin: 0px;
  padding-bottom: 15px;
}

.shop-all-description p {
  width: 276px;
  height: 66px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #724d48;
  margin: 0px;
}

.product-title-component {
  margin-left: 100px;
}

.productlist-parent {
  display: grid;
  grid-template-columns: 0.6fr 1fr;
  grid-template-rows: 1fr;
  grid-column-gap: 0px;
  grid-row-gap: 0px;
}

.shop-all-productlist-div1 {
  grid-area: 1 / 1 / 2 / 2;
}

.productlist-div2 {
  grid-area: 1 / 2 / 2 / 3;
}

