@import url("https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap");

.use-benefit-container {
  margin-top: 30px;
  width: 100%;
}

.useanddescription {
  display: flex;
  justify-content: center;
  gap: 120px;
}

.use-image p {
  width: 60px;
  height: 19px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 137.8%;
  color: #3a383b;
}

.use-image img {
  width: 605px;
  height: 585px;
  left: 120px;
  top: 900px;
}

.description-side {
  margin-top: 80px;
}

.description-side .heading {
  width: 274px;
  height: 41px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 137.8%;
  color: #3a383b;
  text-align: start;
  margin: 0px;
  padding-bottom: 10px;
}

.description-side .caption,
.benefits-caption {
  width: 357px;
  height: 66px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #3a383b;
  text-align: start;
  margin: 0px;
}

.use-line {
  width: 280px;
  height: 0px;
  margin-top: 100px;
  border: 3px solid #000000;
}

.dosage,
.texture,
.aroma {
  width: 274px;
  height: 19px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 137.8%;
  color: #3a383b;
}

.dosage-caption,
.texture-caption,
.aroma-caption {
  width: 389px;
  height: 22px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #919191;
}

.small-line {
  width: 280px;
  height: 0px;
  left: 797px;
  top: 1263px;
  border: 1px solid #e0d5d2;
}

.texture,
.onyourskin {
  width: 95px;
  height: 19px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 137.8%;
  color: #3a383b;
}

.benefitanddescription {
  display: flex;
  justify-content: center;
  gap: 120px;
  margin-left: -70px;
  margin-top: 50px;
}

.benefitanddescription-side {
  margin-top: 70px;
}

.benefit-image img {
  width: 605px;
  height: 417px;
}

.benefitanddescription-side .benefits {
  width: 274px;
  height: 41px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 137.8%;
  color: #3a383b;
  text-align: start;
  margin: 0px;
  padding-bottom: 10px;
}

.benefits-caption {
  width: 324px;
  height: 88px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #3a383b;
  text-align: start;
}

@media only screen and (min-width: 900px) and (max-width: 1199px) {
  .use-benefit-container {
    scale: 0.67;
    margin-left: 20px;
    margin-top: -300px;
  }
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .use-benefit-container {
    scale: 0.9;
    margin-left: -30px;
    margin-top: -100px;
  }
  .description-side {
    margin-left: -20px;
  }

  .benefitanddescription {
    margin-left: -50px;
  }
  .benefitanddescription-side {
    margin-left: -10px;
  }
}

@media only screen and (min-width: 1710px) and (max-width: 1920px) {
  .use-benefit-container {
    margin-left: 200px;
  }
  .benefitanddescription {
    scale: 1;
  }
  .use-image p {
    font-size: 14px;
  }

  .use-image {
    scale: 1.38;
    margin-top: 200px;
    margin-left: -300px;
  }
  .description-side {
    scale: 1.3;
    margin-top: 200px;
    margin-left: 200px;
  }

  .benefit-image {
    scale: 1.38;
    margin-top: 200px;
    margin-left: -300px;
  }

  .benefitanddescription-side {
    scale: 1.3;
    margin-top: 200px;
    margin-left: 200px;
  }
}
@media only screen and (min-width: 768px) and (max-width: 899px) {
  .useanddescription{
    scale: 0.7;
  }
  .benefitanddescription{
    scale: 0.7;
  }
}
