@import url("https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap");

.testimonials {
  margin-top: 300px;
  overflow: hidden;
  position: relative;
}

.testimonials .title {
  width: 100%;
  height: 232px;
  display: flex;
  flex-direction: column;
}

.testimonials .title:hover {
  color: rgb(94, 35, 10);
}

.testimonials .title img {
  position: absolute;
  width: 100%;
}

.testimonials .title p {
  /* bottom: 10px; */
  color: white;
  text-align: center;
  position: absolute;
  font-family: "Prata";
  font-size: 64px;
  font-weight: 400;
  line-height: 65px;
  letter-spacing: -0.01em;
  height: 65px;
  width: 100%;
}

.testimonials .title .line {
  width: 284px;
  height: 1px;
  position: absolute;
  margin: 200px 0px 0px 500px;
  border-bottom: 1px solid #6e4f20;
}

.testimonials .scrolling-wrapper {
  overflow-x: scroll;
  display: flex;
  margin-top: 30px;
  margin-left: 100px;
  float: left;
  height: 330px;
  width: 93%;
  padding: 0;
}

#style::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #f5f5f500;
}

#style::-webkit-scrollbar {
  height: 7px;
  background-color: #f5f5f5fd;
}

#style::-webkit-scrollbar-thumb {
  background-color: #000000e1;
}

.testimonials .card {
  height: 276px;
  width: 418px;
  margin-left: 20px;
  border-right: 1px solid #000000;
  padding: 0px 30px 0px 30px;
}

.testimonials .card .title {
  height: 32px;
  width: 445px;
  font-family: "Prata";
  font-size: 32px;
  font-weight: 400;
  line-height: 32px;
  letter-spacing: -0.01em;
  text-align: left;
}

.testimonials .card .description {
  font-family: "Roboto";
  font-size: 16px;
  font-weight: 400;
  line-height: 26px;
  text-align: left;
  height: 104px;
  width: 418px;
}

.testimonials .logo-box {
  display: flex;
  flex-direction: row;
}

.testimonials .logo-title {
  font-family: "Roboto";
  font-size: 28px;
  font-weight: 400;
  line-height: 28px;
  margin: 0px 15px;
  text-align: left;
  height: 28px;
  width: 117px;
}

.testimonials .logo-caption {
  font-family: "Roboto";
  font-size: 14px;
  font-weight: 400;
  text-align: left;
  margin: 10px 15px;
  height: 14px;
  width: 128px;
  color: #8b5632;
}




@media only screen and (min-width: 767px) and (max-width: 899px) {
  .testimonials .scrolling-wrapper {
    margin: -80px 0px 30px 80px;
    scale: 0.8;
    width: 110%;
    margin-left: 0px;
  }

  .testimonials .title p {
    margin: 40px 0px 0px 120px;
  }

  .testimonials .title .line {
    display: none;
  }
}

@media only screen and (max-width: 767px) {
  .testimonials .scrolling-wrapper {
    margin: -120px 0px 30px 80px;
    scale: 0.8;
    width: 110%;
    margin-left: 0px;
  }

  .testimonials .title p {
    margin: 20px 0px 0px 50px;
    font-size: 50px;
  }

  .testimonials .title .line {
    display: none;
  }
}

@media only screen and (min-width: 900px) and (max-width: 1199px) {
  .testimonials .scrolling-wrapper {
    margin: -80px 0px 30px 80px;
    scale: 0.8;
    width: 110%;
    margin-left: 0px;
  }

  .testimonials .title p {
    margin: 80px 0px 0px 120px;
  }

  .testimonials .title .line {
    margin: 127px 0px 0px 500px;
  }
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .testimonials .scrolling-wrapper {
    margin-left: 80px;
  }

  .testimonials .title p {
    margin: 100px 0px 0px 120px;
  }

  .testimonials .title .line {
    margin: 147px 0px 0px 500px;
  }
  .testimonials .scrolling-wrapper {
    /* margin-top: 150px; */
    margin-bottom: 100px;
  }
}

@media only screen and (min-width: 1710px) and (max-width: 1920px) {
  .testimonials .scrolling-wrapper {
    margin-top: 150px;
    margin-bottom: 100px;
  }
}
