@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat:wght@300&family=Prata&family=Rosario&display=swap");

.bio2-main {
  width: auto;
  margin-bottom: 200px;
}

.bio2-title {
  display: flex;
  justify-content: center;
}

.bio2-title p {
  width: 570px;
  height: 93px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 96px;
  line-height: 96.5%;
  color: #141414;
  align-items: center;
}

.bio2-container {
  display: flex;
  justify-content: space-between;
  margin: 0px 110px;
}

.bio2-caption p {
  width: 603px;
  height: 266px;
  text-align: start;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 157.19%;
  color: #8b5632;
  margin-left: 100px;
}

.bio2-image {
  width: 242px;
  height: 300px;
  margin-top: -100px;
}
.bio2-image:hover {
  scale: 1.05;
}
@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .bio2-title p {
    width: 570px;
    height: 93px;
    font-family: "Prata";
    font-style: normal;
    font-weight: 400;
    font-size: 85px;
    line-height: 96.5%;
    color: #141414;
    align-items: center;
  }
}

@media only screen and (min-width: 900px) and (max-width: 1119px) {
  .bio2-title p {
    width: 570px;
    height: 93px;
    font-family: "Prata";
    font-style: normal;
    font-weight: 400;
    font-size: 75px;
    line-height: 96.5%;
    color: #141414;
    align-items: center;
  }
}
@media only screen and (min-width: 900px) and (max-width: 1119px) {
  .bio2-caption p {
    width: 500px;
    height: 266px;
    text-align: start;
    font-family: "Prata";
    font-style: normal;
    font-weight: 400;
    font-size: 24px;
    line-height: 157.19%;
    color: #8b5632;
    margin-left: -50px;
    margin-top: 10px;
  }
}
@media only screen and (min-width: 768px) and (max-width: 899px) {
  .bio2-container {
    display: flex;
    justify-content: space-between;

    scale: 0.8;
    margin-left: -120px;
    margin-top: -100px;
  }
}
@media only screen and (min-width: 768px) and (max-width: 899px) {
  .bio2-title p {
    width: 570px;
    height: 93px;
    font-family: "Prata";
    font-style: normal;
    font-weight: 400;
    font-size: 55px;
    line-height: 96.5%;
    color: #141414;
    align-items: center;
    scale: 0.7px;
    margin-left: -100px;
  }
}
@media only screen and (min-width: 1710px) and (max-width: 1920px) {
  .bio2-caption p {
    width: 600px;
    height: 266px;
    text-align: start;
    font-family: "Prata";
    font-style: normal;
    font-weight: 400;
    font-size: 25px;
    line-height: 157.19%;
    color: #8b5632;
    margin-left: 40px;
    margin-top: 10px;
  }
  .bio2-image {
    width: 242px;
    height: 300px;
    margin-top: -40px;
    margin-left: 100px;
  }
  

}
