.m-concerns {
  height: 600px;
  padding-left: 20px;
}

.m-concerns-title {
  width: 192px;
  height: 77px;
  text-align: start;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 34px;
  letter-spacing: -0.01em;
  color: #000000;
}

.m-concerns-box {
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  margin-bottom: 30px;
}

.m-concerns-div {
  scale: 0.9;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
}

.m-concerns-div p {
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 108.8%;
  text-align: center;
  color: #000000;
}
