@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");

.youmight-p {
  width: 569px;
  height: 65px;
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 100.8%;
  letter-spacing: -0.01em;
  color: #000000;
  margin-left: 50px;
}

.youmight-scrolling-wrapper {
  overflow-x: scroll;
  display: flex;
  float: left;
  height: 450px;
  width: 93%;
  padding: 0;
  gap: 50px;
  margin-left: 90px;
  margin-bottom: 100px;
}

.youmight-card-image {
  overflow: hidden;
  width: 100%;
  height: 330px;
}

.name-and-price {
  display: flex;
  justify-content: space-between;
}

.youmight-productname {
  width: 221px;
  height: 17px;
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 108.8%;
  text-align: start;
  color: #000000;
}

.youmight-productprice {
  width: 51px;
  height: 18px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 18px;
  letter-spacing: 0.1px;
  color: #474740;
}

.youmight-productrating {
  width: 49px;
  height: 18px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  color: #8f887a;
  margin: 0px;
}
@media only screen and (min-width: 1200px) and (max-width: 1440px){
  .youmight-scrolling-wrapper {

    scale: 0.9;
  }
  .youmight-p {
    
    margin-left: 100px;
  }

}
@media only screen and (min-width: 900px) and (max-width: 1199px){
  .youmight-scrolling-wrapper {
 
    scale: 0.8;
    margin-left: 10px;
  }
  .youmight-p {
    margin-bottom: 0px;
    margin-left: 50px;
  }


}
@media only screen and (min-width: 768px) and (max-width: 899px){
  .youmight-scrolling-wrapper {
  
    scale:0.8;
    margin-left: 10px;

   
  }
  .youmight-p {
    margin-bottom: 0px;
    margin-left: 30px;
  }


}