@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Prata&family=Roboto&family=Rosario&display=swap");

.founders-container {
  margin-top: 30px;
  width: auto;
  padding-bottom: 30px;
}

.founders-title {
    display: flex;
    justify-content: center;
}

.founders-title p{
  width: 657px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 95px;
  line-height: 96.5%;
  color: #141414;
  margin: 0px;
}

.founders-cards {
  display: flex;
  flex-direction: row;
  justify-content: center;
  gap: 30px;  
  margin-top: 70px;
}

.founder-individual {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 303px;
  height: 565px;
}

.founder-name {
  width: 116px;
  height: 28px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 100.8%;
  text-align: center;
  letter-spacing: -0.01em;
  display: flex;
  justify-content: center;
  margin: 10px 0px;
}

.founder-position {
  width: 207px;
  height: 14px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 100.8%;
  letter-spacing: -0.01em;
  color: #8b5632;
  display: flex;
  justify-content: center;
  margin: 0px 0px;
}

.founder-intro {
  width: 302px;
  height: 126px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 131.8%;
  text-align: center;
  letter-spacing: -0.01em;
  color: #724d48;
  display: flex;
  justify-content: center;
  margin: 20px 0px;
}
@media only screen and (min-width: 1200px) and (max-width: 1440px){
  .founders-container {
    margin-top: 30px;
    width: auto;
    padding-bottom: 30px;
    scale:0.9;
  }

}
@media only screen and (min-width: 900px) and (max-width: 1119px){
  .founders-container {
    margin-top: 30px;
    width: auto;
    padding-bottom: 30px;
    scale:0.9;
  }

}
@media only screen and (min-width: 768px) and (max-width: 899px){
  .founders-container {
    margin-top: -100px;
    width: auto;
    padding-bottom: 30px;
    scale:0.7;
  }
}
  @media only screen and (min-width: 1710px) and (max-width: 1920px){
    .founders-cards {
      display: flex;
      flex-direction: row;
      justify-content: center;
      gap: 50px;  
      margin-top: 70px;
    }
    

}
