@import url("https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap");

.productdes-container {
  margin-top: 10px;
  height: 729px;
  padding-left: 50px;
}

.productdes-title {
  width: 447px;
  height: 41px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 137.8%;
  color: #3a383b;
  text-align: start;
  margin: 0px;
}

.productdes-review-count {
  width: 276px;
  height: 22px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #724d48;
  text-align: start;
  margin: 0px;
}

.productdes-stars {
  width: 100px;
  height: 15px;
}

.productdes-description {
  width: 399px;
  height: 84px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 153.19%;
  color: #3a383b;
}

.productdes-price {
  height: 21px;
  padding: 5px 0px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 26px;
  line-height: 85.8%;
  display: flex;
}

.productdes-offers {
  width: 114px;
  height: 22px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  margin: 0px;
  color: #919191;
}

.productdes-line {
  width: 280px;
  height: 0px;
  margin: 10px 0px;
  border: 2px solid #000000;
}

.productdes-small-line {
  width: 280px;
  height: 0px;
  margin: 5px 0px;
  border: 0.5px solid #e0d5d2;
}

.productdes-heading {
  width: 274px;
  height: 19px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 600;
  font-size: 16px;
  line-height: 137.8%;
  margin: 0px;
  color: #3a383b;
  padding: 5px 0px;
}

.productdes-caption {
  width: 193px;
  height: 22px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #919191;
  margin: 0px;
  padding: 10px 0px;
}

.productdes-add-ingredients {
  width: 280px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}

.plusellipse {
  padding-bottom: 2px;
}

.plusellipse:hover {
  cursor: pointer;
}

.productdes-quantity {
  display: flex;
  flex-direction: row;
}

.productdes-quantity p {
  width: 41px;
  height: 18px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 12px;
  line-height: 18px;
  display: flex;
  align-items: center;
  letter-spacing: 0.1px;
  color: #3a383b;
  margin: 0px;
  padding: 5px 5px;
}

.quantity-div1 {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 45px;
  height: 20px;
  border: 1px solid #3a383b;
  border-radius: 20px 0px 0px 20px;
  margin: 15px 0px;
}

.quantity-div2 {
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 50px;
  height: 22px;
  border: 1px solid #3a383b;
  border-radius: 0px 20px 20px 0px;
  margin: 15px 0px;
}

.quantity-div1,
.productdes-quantity:hover {
  cursor: pointer;
}

.productdes-buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 399px;
}

.productdes-cart-btn button {
  margin-top: 10px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  position: absolute;
  width: 200px;
  height: 57px;
  border: none;
  background: #3a383b;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 85.8%;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
}

.number-quantity {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 54px;
  border: 1px solid #000000;
  padding: 0px 0px;
}

.number-quantity .product-itemcount {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 85.8%;
  display: flex;
  color: #000000;
}

.itemcount-icon {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  display: flex;
  color: #000000;
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .productdes-container {
    margin-left: -30px;
  }
}

@media only screen and (min-width: 1710px) and (max-width: 1920px) {
  .productdes-container {
    scale: 1.4;
    width: 300px;
    margin-left: 150px;
  }
}
