.how-main {
  width: 100%;
  overflow: hidden;
}

.how p {
  width: 100%;
  height: 93px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 80px;
  line-height: 96.5%;
  color: #141414;
  text-align: center;
}

.howdes p {
  width: 603px;
  height: 266px;
  text-align: start;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 30px;
  line-height: 157.19%;
  color: #8b5632;
}

.howimg {
  width: 500px;
  height: 600px;
  overflow: hidden;

  margin-top: 50px;
}
.howimg :hover {
  scale: 1.05;
}

.how-description {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding: 0px 0px 0px 150px;
  margin-top: 50px;
}
