.userreview-container-main {
  width: 100%;

}

.userreview-container {
  padding: 50px;
}

.userreview-title {
  width: 569px;
  height: 65px;
  padding: 10px 0px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 100.8%;
  text-align: start;
  letter-spacing: -0.01em;
  color: #000000;
  margin: 0px;
}

.userreview-count {
  width: 276px;
  height: 22px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  padding: 5px 0px;
  color: #724d48;
  margin: 0px;
}

.userreview-rating {
  width: 64px;
  height: 22px;
  text-align: start;
  padding: 10px 0px;
  color: #724d48;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  margin: 0px;
}

.userreview-mostrelevent {
  width: 100%;
  display: flex;
  justify-content: space-between;
  padding: 5px 0px;
}

.userreview-dropdown select,
option {
  width: 142px;
  height: 22px;
  border: none;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #724d48;
}

.userreview-stars {
  width: 300px;
  height: 15px;
  display: flex;
  flex-direction: row;
  margin-top: 9px;
}

.userreview-stars p {
  width: 64px;
  height: 22px;
  padding: 0px 10px;
  margin-top: -1px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #724d48;
}

.userreview-small-line {
  width: 1140px;
  height: 0px;
  margin: 10px 0px 10px 0px;
  border: 1px solid #e0d5d2;
}

.user-reviews {
  width: 100%;
  display: flex;
  flex-direction: row;
  padding-bottom: 30px;
  padding-top: 10px;
}

.userimage {
  width: 57px;
  height: 57px;
  left: 120px;
}

.userreview-userdetails {
  width: 200px;
  padding-left: 20px;
}

.userreview-name {
  width: 274px;
  height: 19px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 137.8%;
  color: #3a383b;
  text-align: start;
}

.userreview-userstatus {
  width: 84px;
  height: 19px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 300;
  font-size: 14px;
  line-height: 137.8%;
  color: #3a383b;
  text-align: start;
}

.userreview-review {
  width: 800px;
}

.userreview-comment {
  width: 502px;
  height: 88px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #919191;
  text-align: start;
}

.userreview-date {
  width: 300px;
  display: flex;
  justify-content: right;
}

.userreview-date p {
  width: 80px;
  height: 19px;
  text-align: end;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 137.8%;
  text-align: start;
  color: #3a383b;
  padding-left: 30px;
}

.review-pages img {
  display: flex;
  flex-direction: row;
}

.morereviews-box {
  position: absolute;
  width: 71px;
  height: 19px;
  left: 1223px;
  top: 3241px;

  font-family: "Roboto";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 137.8%;
  color: #000000;
}

.morereviews-box p {
  opacity: 0.1;
}

.morereviews-back-btn {
  padding-right: 10px;
}

@media only screen and (min-width: 900px) and (max-width: 1199px) {
  .userreview-container {
    scale: 0.75;
    margin-left: -20px;
    margin-top: -250px;
    margin-bottom: -100px;
  }

  .userreview-small-line {
    width: 1000px;
  }

  .userreview-date {
    width: 600px;
    margin-left: 100px;
  }

  .morereviews-box {
    left: 900px;
    top: 951px;
  }

  .userreview-mostrelevent {
    width: 1000px;
  }
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .userreview-container {
    scale: 0.96;
    margin-left: 70px;
    margin-top: 0px;
  }

  .morereviews-box {
    left: 900px;
    top: 951px;
  }

  .userreview-title {
    font-size: 58px;
  }

  .userreview-small-line {
    width: 1000px;
  }
}

@media only screen and (min-width: 1710px) and (max-width: 1920px) {
  .userimage {
    margin-top: 20px;
    margin-right: 10px;
    scale: 1.4;
  }

  .userreview-name {
    font-size: 18px;
    width: 374px;
  }

  .userreview-userstatus {
    font-size: 16px;
    width: 104px;
  }

  .userreview-small-line {
    width: 1620px;
  }

  .userreview-review {
    padding-left: 100px;
    width: 1200px;
  }

  .userreview-stars {
    width: 400px;
    height: 25px;
    display: flex;
    flex-direction: row;
    margin-top: 9px;
  }

  .user-reviews {
    padding-bottom: 50px;
    padding-top: 10px;
  }

  .userreview-stars p {
    width: 84px;
    font-size: 20px;
  }

  .userreview-comment {
    font-size: 22px;
    width: 800px;
  }

  .userreview-date {
    margin-right: 20px;
  }

  .userreview-date p {
    font-size: 18px;
  }

  .morereviews-box {
    left: 1680px;
    top: 4050px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 899px) {
  .userreview-container {
    scale: 0.7;
    margin-left: -70px;
  }

  .userreview-small-line {
    width: 100%;
  }
}