@import url("https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap");

.receipt-container {
  width: 100%;
  height: 970px;
  background: rgba(227, 217, 198, 0.19);
}

.receipt-header {
  position: relative;
  width: 100%;
}
.receipt-header .receipt-backgroundimage{
  width: 100%;
  object-fit: cover;
}

.receipt-main {
  position: absolute;
  display: flex;
  flex-direction: row;
  top: 160px;
  left: 70px;
  width: 842px;
}

.receipt-main p {
  width: 346px;
  height: 42px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  line-height: 105.8%;
  text-align: center;
  color: #000000;
  margin: 0px;
  padding-left: 20px;
}

.receipt-caption {
  position: absolute;
  top: 240px;
  left: 70px;
  display: flex;
  flex-direction: column;
}

.receipt-caption .receipt-caption1,
.receipt-caption2 {
  width: 534px;
  height: 126px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 22px;
  line-height: 35px;
  letter-spacing: -0.2px;
  color: #8f887a;
  text-align: start;
  margin: 0px;
}

.receipt-caption .receipt-caption2 {
  margin-top: -30px;
}

.receipt-caption .receipt-caption2 a {
  color: #3a383b;
}

.receipt-item-quantity {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 780px;
  margin-left: 70px;
  margin-top: 30px;
}

.receipt-item-quantity-row3 {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 520px;
  margin-left: 70px;
  margin-top: 30px;
}

.receipt-item {
  width: 125px;
  height: 22px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 137.8%;
  color: #000000;
  text-align: start;
  padding-top: 30px;
  margin: 0px;
  padding-bottom: 10px;
}

.receipt-item-name {
  width: 254px;
  height: 28px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 137.8%;
  color: #3a383b;
  text-align: start;
  margin: 0px;
}

.receipt-item-name-row3 {
  width: 200px;
  height: 28px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 137.8%;
  color: #3a383b;
  text-align: start;
  margin: 0px;
}

.receipt-whatsapp {
  margin: 80px 60px 30px 60px;
  width: 368px;
  height: 20px;
  display: flex;
  flex-direction: row;
}

.receipt-whatsapp p {
  width: 250px;
  height: 20px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 18px;
  line-height: 105.8%;
  color: #3a383b;
  margin: 0px -20px;
}

.receipt-download {
  margin-left: 70px;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  width: 660px;
  height: 60px;
  background: #3a383b;
}

.receipt-download p {
  width: 161px;
  height: 17px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 85.8%;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
}
