@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");

.shopmain {
  margin-left: 100px;
}

.apply {
  display: flex;
  justify-content: space-between;
  width: 276px;
  height: 35px;
  margin-top: 0px;
}

.apply .filter {
  width: 128px;
  height: 14px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 85.8%;
  color: #3a383b;
}

.filter-selected {
  margin-top: 20px;
  height: 120px;
}

.filter-dryskin {
  width: 78px;
  height: 22px;
}

.filter-dryskin p {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  color: #724d48;
}

.filter-component .close {
  width: 11px;
  height: 11px;
  font-weight: bold;
  color: #000000;
  display: flex;
  justify-content: center;
  align-items: center;
}

.clear {
  width: 60px;
  height: 14px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 85.8%;
  text-decoration-line: underline;
  color: #8b5632;
}

.filter-component {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 5px 10px;
  margin: 10px 0px;
  gap: 25px;
  width: 120px;
  height: 22px;
  background: #f5e8d6;
  border-radius: 34px;
}

.rate {
  width: 78px;
  height: 22px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #724d48;
}

.shopall-category {
  display: flex;
  flex-direction: column;
  justify-content: start;
  margin-top: 50px;
}

.shopall-category p {
  width: 81px;
  height: 14px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 85.8%;
  color: #3a383b;
  text-align: start;
  padding-bottom: 20px;
  margin: 0px;
}

.shopall-category input[type="checkbox"] {
  box-sizing: border-box;
  width: 17px;
  height: 17px;
  background: #b17668;
  border: 1px solid #000000;
  border-radius: 2px;
}

.shopall-category input[type="checkbox"]:checked:after {
  color: #b17668;
}

.shopall-category .main {
  position: relative;
  margin-left: 1px;
  margin-bottom: 10px;
  cursor: pointer;
  align-items: center;
  display: flex;
  width: 174px;
  height: 20px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 105.8%;
  color: #8b5632;
}

.shopall-category .check-main {
  text-align: start;
  width: 200px;
  height: 10px;
  margin-bottom: 50px;
  text-align: center;
}

input[type="checkbox"] {
  visibility: hidden;
}

.geekmark {
  position: absolute;
  top: 0;
  left: 0;
  height: 12px;
  width: 12px;
  border-radius: 2px;
  border: solid black;
  background-color: rgba(82, 141, 214, 0);
}

.main input:checked ~ .geekmark {
  background-color: #b17668;
}

.geekmark:after {
  content: "";
  position: absolute;
  display: none;
}

.main input:checked ~ .geekmark:after {
  display: block;
}

.main .geekmark:after {
  left: 8px;
  bottom: 5px;
}
