.payment-page-parent {
    background: #FFFEF3;
    padding-bottom: 300px;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    grid-column-gap: 0px;
    grid-row-gap: 0px;
}

.payment-page-div1 {
    grid-area: 1 / 1 / 2 / 2;
    padding: 0px 30px 30px 50px;
    background: rgba(227, 217, 198, 0.19);
}

.payment-title {
    height: 68px;
    width: 271px;
    font-family: Prata;
    font-size: 64px;
    font-weight: 400;
    text-align: center;
    margin: 40px 0px 0px 0px;
}

.payment-caption {
    font-family: Roboto;
    /* font-size: 20px; */
    font-weight: 400;
    text-align: left;
    font-size: 24px;
    font-weight: 400;
    height: 26px;
    width: 625px;
    margin: 30px 0px 0px 0px;
    color: #8F887A;

}

.payment-tabs {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 20px;
}

.payment-page-input-box {
    display: flex;
    flex-direction: column;
}

.continue-shipping {
    width: auto;
    margin-top: 20px;
    border: none;
    font-family: Roboto;
    font-size: 20px;
    font-weight: 400;
    background: #3A383B;
    color: #FFFFFF;
    padding: 10px 200px 10px 200px;
    text-decoration: none;
}

.back-to-information {
    margin-top: 50px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 15px;
    line-height: 26px;
}
.pay-continue-btn{
    margin-top: 200px;
    border: none;
    font-family: Roboto;
    font-size: 20px;
    font-weight: 400;
    background: #3A383B;
    color: #FFFFFF;
    padding: 10px 200px 10px 200px;
    text-decoration: none;
}

.pay-now-btn{
    margin-top: 20px;
    border: none;
    font-family: Roboto;
    font-size: 20px;
    font-weight: 400;
    background: #3A383B;
    color: #FFFFFF;
    padding: 10px 200px 10px 200px;
    text-decoration: none;
}

.payment-tab {
    font-family: Roboto;
    font-size: 24px;
    font-weight: 400;
    line-height: 26px;
    letter-spacing: -0.20000000298023224px;
    text-align: left;
}

.payment-tab-active {
    font-family: Roboto;
    font-size: 24px;
    font-weight: 700;
    line-height: 26px;
    letter-spacing: -0.20000000298023224px;
    text-align: left;
    text-decoration-line: underline;
}






.payment-page-div2 {
    grid-area: 1 / 2 / 2 / 3;
}