@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");

.myprofileinfo-main {
  margin-bottom: 100px;
}

.myprofileinfo-text-main {
  display: flex;
  justify-content: center;
  flex-direction: column;
  width: 319px;
  height: 68px;
  margin-left: 20px;
  margin-top: 100px;
  text-align: start;
}

.myprofile-elipse {
  margin-top: 20px;
}

.myprofileinfo-text {
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 105.8%;
  color: #000000;
  display: flex;
  text-align: center;
  margin: 0px;
}

.myprofile-grids {
  display: flex;
  flex-direction: row;
  margin-left: 20px;
  margin-top: 20px;
}

.myprofile-verified {
  margin-top: -20px;
}

.myprofile-verified img {
  width: 20px;
  height: 20px;
  margin-top: -40px;
}

.myprofile-name {
  width: 101px;
  height: 21px;
  margin: 20px 0px 10px 50px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 85.8%;
  text-align: start;
  color: #3a383b;
}

.myprofile-namediv {
  margin-left: 50px;
  width: 376px;
  display: flex;
  justify-content: space-between;
  padding-bottom: 5px;
}

.myprofile-dob {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #976e52;
  align-items: center;
  text-align: center;
  margin: 0px;
}

.myprofile-edit {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  display: flex;
  text-decoration-line: underline;
  color: #8b5632;
  margin: 0px;
}

.myprofile-line {
  width: 376px;
  margin-top: 10px;
  margin-left: 50px;
  border: 3px solid #000000;
}

.myprofile-email,
.myprofile-phoneno {
  width: 274px;
  height: 19px;
  margin-top: 20px;
  margin-left: 50px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 600;
  font-size: 14px;
  line-height: 137.8%;
  color: #3a383b;
  text-align: start;
}

.myprofile-emaildiv,
.myprofile-phonediv {
  width: 280px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  text-align: center;
  margin-left: 55px;
}

.myprofile-person-email,
.myprofile-person-number {
  width: 136px;
  height: 22px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  text-align: start;
  color: #976e52;
  margin: 0px;
}

.myprofile-small-line {
  width: 280px;
  height: 0px;
  margin-left: 50px;
  margin-top: 10px;
  border: 1px solid #e0d5d2;
}

.address-small-line {
  width: 376px;
  height: 0px;
  border: 1px solid #e0d5d2;
  margin-top: 10px;
}

.address-main {
  margin-left: 30px;
  width: 400px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  margin-top: 90px;
}

.addnew {
  width: 88px;
  height: 17px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  display: flex;
  align-items: center;
  text-align: center;
  color: #8b5632;
}

.saveadress {
  width: 187px;
  height: 21px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 85.8%;
  margin: 0px;
}

.address-container {
  margin-top: 20px;
  margin-left: 50px;
}

.place-edit {
  width: 380px;
  display: flex;
  justify-content: space-between;
}

.place-frame {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 90px;
  height: 45px;
  background: #724d48;
}

.myprofile-place {
  width: 64px;
  height: 15px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 137.8%;
  color: #fffef3;
}

.myprofile-icons {
  width: 55px;
  height: 20px;
  display: flex;
  justify-content: space-between;
}

.temporary-address,
.permanant-address {
  width: 181px;
  height: 88px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 139.19%;
  color: #000000;
  padding-top: 10px;
  margin: 0px;
}

.myprofile-phonenumber {
  text-align: start;
  margin: 0px;
  width: 126px;
  height: 22px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 139.19%;
  color: #000000;
}