@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");

.contactus-pic{
  width: 100%;
  /* height: 270px; */
  overflow: hidden;
}
.contactus-pic img{
  object-fit: cover;
}

.contactus-txt {
  height: 63px;
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 97.8%;
  text-align: center;
  letter-spacing: -0.01em;
  color: #e3d9c6;
  margin-top: -170px;
}

.contactus-main {
  width: 100%;
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items:center;
}

.contactus-frame {
  padding: 30px 50px;
  width:fit-content;
  height: fit-content;
  background: #f4ecdd;
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: center;
  border-radius: 5px;
  margin: 100px 100px;
  margin-left: 180px;
}

.contactinfo p {
  width: 100%;
  height: 45px;
  /* padding-bottom: 10; */
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 40px;
  text-align: center;
  color: #4d3e2f;
}

.contactdes p{
  width: 581px;
  height: 114px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 157.19%;
  text-align: center;
  color: #724d48;
  display: flex;
  justify-content: center;
  align-items: center;
}

.contactus-dropdown select {
  box-sizing: border-box;
  width: 629px;
  height: 60px;
  background: #ffffff;
  border: 1px solid #c1b8a8;
  border-radius: 3px;
  margin-bottom: 20px;
}

.contactus-dropdown select option {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 10px;
  background: #ffffff;
  min-width: 629px;
}

.dropdownoption {
  display: none;
  position: absolute;
  background-color: #f1f1f1;
  min-width: 10px;
  overflow: auto;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
  z-index: 1;
}

.sendbtn {
  margin-top: 70px;
  align-items: center;
  padding: 25px 17px;
  gap: 10px;
  width: 409px;
  height: 67px;
  color: #ffffff;
  font-family: "Roboto";
  font-style: normal;
  background: #3a383b;
}

.message {
  box-sizing: border-box;
  width: 629px;
  height: 179px;
  background: #ffffff;
  border: 1px solid #c1b8a8;
  border-radius: 3px;
}

#id1 {
  width: 100%;
}
@media only screen and (min-width: 1200px) and (max-width: 1440px) {
 
} 
@media only screen and (min-width: 900px) and (max-width: 1199px) {
  .contactus-frame {
    scale: 0.9;
  }
  .contactus-txt {
    scale: 0.8;
  }
}
@media only screen and (min-width: 768px) and (max-width: 899px) {
  .contactus-txt {
    scale: 0.7;
  }
  .contactus-frame {
    scale: 0.8;
  }
}
