@import url("https://fonts.googleapis.com/css2?family=Libre+Baskerville&family=Montserrat&family=Roboto&display=swap");
.blog-main{
  overflow: hidden;
}
.blogpic {
  position: relative;
  display: flex;
  justify-content: center;
  align-items: center;
  height: auto;
  overflow: hidden;
}

.blogtxt {
  position: absolute;
  /* top: 10%; */
  /* left: 48%; */
  height: 63px;
  font-family: "Baskerville";
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  text-align: center;
  letter-spacing: -0.01em;
  color: #724d48;
}

.blogsubtxt p {
  margin-left: 20px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 85.8%;
  color: #b6ada3;
}

.blog-hair {
  width: 379px;
  height: 687px;
}

.bloghair-container {
  flex-direction: row;
}

.prodate {
  width: 221px;
  height: 17px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 108.8%;
  color: #b6ada3;
}

.productdes {
  width: 365px;
  height: 31px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 127.8%;
  color: #724d48;
  margin-left: -17px;
}

.blogprodesc {
  width: 342px;
  height: 100px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 127.8%;
  color: #976d50;
  margin-left: -9px;
}

.blog-btn {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 16px;
  width: 379px;
  height: 38px;
  background: #e3d9c6;
  border-radius: 42px;
}

.readdmoretxt {
  width: 97px;
  height: 18px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 18px;
  letter-spacing: 0.1px;
  color: #724d48;
}

.blog-loadmorebtn {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 30px;
}

.blog-loadmorebtn p {
  width: 245px;
  height: 67px;
  border: 1px solid #000000;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  color: #3a383b;
  display: flex;
  justify-content: center;
  align-items: center;
}

.column {
  overflow: hidden;
  width: 379px;
  height: 657;
  margin-top: 70px;
  margin-bottom: 50px;
}

.blog-row {
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  margin-left: 70px;
}

.blog-row::after {
  content: "";
  clear: both;
  display: table;
}

.descmain {
  padding: 10px;
  column-gap: 40px;
}

.blog-line {
  width: 377px;
  height: 0px;
  border: 1px solid #b6ada3;
}

.blog-cap1 {
  width: 221px;
  height: 17px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 108.8%;
  color: #b6ada3;
}

.blog-cap2 {
  width: 219px;
  height: 17px;
  font-family: "Roboto";
  font-style: "normal";
  font-weight: 400;
  font-size: "16px";
  line-height: 108.8%;
  width: 365px;
  height: 17px;
  font-family: "Roboto";
  font-style: "normal";
  font-weight: 700;
  font-size: 22px;
  line-height: 127.8%;
  text-align: start;
  color: "#724D48";
  color: #724d48;
}

.blog-cap3 {
  width: 379px;
  height: 100px;
  text-align: start;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 15.5px;
  line-height: 127.8%;
  color: #976d50;
}

@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .blog-row {
    scale: 0.9;
    gap: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
  }
  .blog-btn {
    width: 345px;
    height: 38px;
  }
  .blog-cap3 {
    width: 355px;
    height: 100px;
  }
  .blog-loadmorebtn p {
    margin-left: 70px;
    margin-top: -30px;
  }
}

@media only screen and (min-width: 900px) and (max-width: 1199px) {
  .blog-row {
    scale: 0.9;
    gap: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-left: 20px;
    margin-top: -30px;
  }
  .column {
    overflow: hidden;
  }
  .blog-btn {
    width: 290px;
    height: 38px;
  }
  .blog-line {
    width: 315px;
  }

  .blog-cap1 {
    width: 315px;
  }
  .blog-cap2 {
    width: 280px;
    padding-bottom: 20px;
  }
  .blog-cap3 {
    width: 290px;
    height: 100px;
    padding-bottom: 10px;
  }

  .blog-loadmorebtn p {
    scale: 0.8;
    margin-top: -40px;
  }
}

@media only screen and (min-width: 768px) and (max-width: 899px) {
  .blog-row {
    scale: 0.9;
    gap: 20px;
    display: flex;
    flex-direction: row;
    justify-content: space-evenly;
    margin-left: 25px;
    margin-top: -30px;
  }
  .column {
    overflow: hidden;
  }
  .column img {
    height: 300px;
  }
  .blog-btn {
    width: 220px;
    height: 38px;
  }
  .blog-cap2 {
    width: 220px;
    padding-bottom: 20px;
  }
  .blog-cap3 {
    width: 220px;
    height: 100px;
    margin-bottom: 50px;
  }

  .blog-loadmorebtn p {
    scale: 0.8;
    margin-top: -40px;
  }
}

@media only screen and (min-width: 1441px) and (max-width: 1920px) {
  .blogpic {
    width: 1901px;
    overflow: hidden;
  }
  .blogpic img {
    width: 1900px;
  }
  .blogtxt {
    top: 5%;
    font-size: 84px;
  }
  .blog-row {
    margin-left: 150px;
    margin-top: 40px;
  }
  .column {
    width: 450px;
    overflow: hidden;
  }
  .column img {
    width: 450px;
  }
  .blog-btn {
    width: 450px;
    height: 58px;
    margin-top: -20px;
  }
  .blog-cap2 {
    font-size: 28px;
    width: 500px;
    padding-bottom: 20px;
  }
  .blog-cap3 {
    width: 440px;
    height: 100px;
    margin-bottom: 50px;
    font-size: 20px;
  }
  .blog-line {
    width: 515px;
  }
  .blog-loadmorebtn p {
    margin-left: 100px;
  }
}
