.ourstorymob-main {
  position: relative;

  background: #fffef3;
  width:100%;
  overflow: hidden;
}
.ourstorymob-pic {
  background-position: center bottom;

  width: 100%;
  height: 161px;
  background-size: cover;

  background-image: url("../../../assets/images/ourstory.svg");
}
.ourstorymob-textdiv {
  position: absolute;
  width: 100%;
  height: 538px;
  top: 0px;
  position: relative;
  background: #8b5632;
}
.ourstorymob-text {
  position: absolute;
  width: 232px;
  height: 189px;
  top: -10px;
  left: 40px;

  font-family: 'Baskerville';
  font-style: normal;
  font-weight: 400;
  font-size: 64px;
  line-height: 56px;
  /* or 88% */
  text-align: start;

  letter-spacing: -0.01em;

  color: #e3d9c6;
}
.ourstorymob-founder {
  position: absolute;
  width: 50%;
  height: 66px;
  left: 75px;
  top: 350px;

  font-family: 'Prata';
  font-style: normal;
  font-weight: 400;
  font-size: 24px;
  line-height: 34px;
  /* or 144% */

  text-align: center;
  letter-spacing: -0.01em;

  color: #e1c393;
}
.ourstorymob-skincareframe {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  padding: 10px 10px 0px;
  gap: 10px;

  position: absolute;
  width: 65%;
  height: 126px;
  left: 50px;
  top: 460px;

  background: #dfccae;
  border-radius: 12px;
}
.ourstorymob-skintext {

  height: 116px;

  font-family: 'Rosario';
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 131.8%;
  /* or 26px */

  text-align: center;
  letter-spacing: -0.01em;

  color: #724d48;
}
.ourstorymob-line {
  position: absolute;
  width: 60%;
  height: 0px;
  left: 270px;
  top: 350px;

  border: 1px solid rgba(227, 217, 198, 0.29);
  transform: rotate(-90deg);
}
.ourstorymob-scroll {
  position: absolute;
    font-family: 'Montserrat';
    font-style: normal;
    font-weight: 500;
    font-size: 10px;
    line-height: 85.8%;
    letter-spacing: 0.385em;
    color: #e3d9c6;
    transform: rotate(-90deg);
    left: 380px;
    margin-top: -150px;
}
.line-scroll{
  display: flex;
  flex-direction: column;
  width: 100%;
}
