.shipping-tab {
    display: block;
    position: relative;
}

.shipping-information {
    float: left;
    margin-top: 30px;
    font-family: 'Roboto';
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    color: #000000;
}

.shippingtab-input-box {
    display: flex;
    flex-direction: column;
    width: 80%;
    background-color: rgb(255, 255, 255);
}

.standard-shipping {
    border: 1px solid #C1B8A8;
    display: flex;
}

.cash-on-delivery {
    display: flex;
    border: 1px solid #C1B8A8;
}