@import url("https://fonts.googleapis.com/css2?family=Prata&family=Roboto&display=swap");

.inspiration-container {
  margin-top: 100px;
}

.inspiration-title p {
  width: 370px;
  height: 102px;
  font-family: "Prata";
  font-style: normal;
  font-weight: 400;
  font-size: 48px;
  line-height: 100.8%;
  color: #000000;
  text-align: left;
  display: flex;
  flex-direction: row;
  margin: 0px 0px 0px 100px;
}

.inspiration-title p:hover {
  color: rgb(94, 35, 10);
}

.inspiration-line {
  float: right;
  width: 958px;
  border-bottom: 1px solid #8b5632;
}

.inspiration-parent {
  width: 100%;
  height: max-content;
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, 1fr);
  grid-column-gap: 0px;
  grid-row-gap: 0px;
  margin-top: 50px;
  overflow: hidden;
}

.inspiration-div1 {
  grid-area: 1 / 1 / 2 / 2;
  margin-left: 100px;
  width: 100%;
}

.inspiration-div1 .inspiration-title-subtitle {
  height: 30px;
  float: left;
  font-family: "Roboto";
  font-weight: 400;
  font-size: 28px;
  color: #8b5632;
  margin: 0px;
  padding-bottom: 30px;
}

.inspiration-div1 .inspiration-caption1 {
  width: 100%;
  height: 112px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 28px;
  line-height: 100.8%;
  text-align: start;
  letter-spacing: -0.01em;
  float: left;
  color: #8b5632;
  margin: 0px;
  padding-bottom: 20px;
}

.inspiration-caption2 {
  width: 370px;
  height: 175px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 157.19%;
  float: left;
  text-align: start;
  color: #8f887a;
}

.inspiration-div2 {
  grid-area: 1 / 2 / 2 / 3;
  overflow: hidden;
  object-fit: contain;
  padding-right: 70px;
}

.inspiration-div2 img {
  width: 100%;
  /* height: auto; */
  margin-top: 0px;
}

.inspiration-div3 {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 300px;
  grid-area: 2 / 1 / 3 / 3;
  overflow: hidden;
  background-image: url("../../images/inspirationbottles.svg");
  background-repeat: no-repeat;
  background-size: cover;

}

@media only screen and (max-width: 767px) {
  .inspiration-container {
    overflow: hidden;
  }

  .inspiration-line {
    float: right;
    width: 358px;
  }

  .inspiration-container {
    margin-left: -70px;
  }

  .inspiration-parent {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
  }

  .inspiration-div1 {
    grid-area: 1 / 1 / 2 / 2;
  }

  .inspiration-div2 {
    grid-area: 2 / 1 / 3 / 2;
  }

  .inspiration-div3 {
    grid-area: 3 / 1 / 4 / 2;
  }
}

@media only screen and (min-width: 767px) and (max-width: 900px) {

  .inspiration-line {
    float: right;
    width: 358px;
  }

  .inspiration-container {
    margin-left: -70px;
  }
  .inspiration-parent {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(3, 1fr);
    grid-column-gap: 0px;
    grid-row-gap: 0px;
  }

  .inspiration-div1 {
    grid-area: 1 / 1 / 2 / 2;
  }

  .inspiration-div2 {
    grid-area: 2 / 1 / 3 / 2;
  }

  .inspiration-div3 {
    grid-area: 3 / 1 / 4 / 2;
  }
}

