@media only screen and (min-width: 1200px) and (max-width: 1440px) {
  .inspiration-title p {
    width: 450px;
    font-size: 48px;
  }

  .inspiration-div1 .inspiration-title-subtitle {
    width: 450px;
    text-align: start;
    font-size: 28px;
    margin: 0px;
  }

  .inspiration-div1 .inspiration-caption1 {
    width: 450px;
    font-size: 28px;
    margin: 0px;
  }

