@import url("https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap");

.orderedproduct-container {
  margin-top: 20px;
  margin-left: 30px;
}

.orderedproduct-container p {
  width: 435px;
  height: 112px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 41px;
  line-height: 137.8%;
  color: #3a383b;
  text-align: start;
}

.orderedproduct-title {
  display: flex;
  flex-direction: row;
  margin-left: 30px;
}

.orderedproduct-title .ordered-product-name {
  width: 160px;
  height: 84px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 137.8%;
  color: #3a383b;
}

.orderedproduct-title .ordered-product-quantity {
  width: 160px;
  height: 28px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 137.8%;
  color: #3a383b;
}

.product-line {
  width: 457px;
  height: 0px;
  margin: 50px 0px 10px 0px;
  border: 1px solid #c7c7c7;
}

.ordered-product {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 30px;
}

.ordered-product .orderedproduct-youpaid {
  width: 78px;
  height: 28px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 137.8%;
  color: #000000;
  text-align: start;
}

.ordered-product .orderedproduct-price {
  width: 81px;
  height: 50px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 137.8%;
  color: #000000;
  margin-top: 10px;
  text-align: start;
}

.OrderConfirmed-button {
  margin-right: 60px;
  float: right;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 268px;
  height: 67px;
  background: #3a383b;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
  margin-bottom: 100px;
}

.OrderConfirmed-button p {
  width: 128px;
  height: 17px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  display: flex;
  align-items: center;
  text-align: center;
  color: #ffffff;
}
