@import url('https://fonts.googleapis.com/css2?family=Prata&family=Roboto:wght@100&display=swap');

.payment-sidecart {
  width: fit-content;
}

.cart-detail {
  display: flex;
  flex-direction: row;
  margin-left: 50px;
  margin-top: 50px;
}

.product {
  margin-top: 20px;
  text-align: start;
}

.productname {
  display: flex;
  justify-content: space-between;
  width: 400px;
}

.productname p {
  text-align: start;
  margin: 0px;
  width: 254px;
  height: 28px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  color: #3a383b;
  padding-bottom: 5px;
}

.productoffer {
  width: 178px;
  height: 19px;
  top: 25px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  color: #bbbbbb;
  margin: 0px;
  padding-bottom: 10px;
}

.sidecart-number-quantity {
  display: flex;
  flex-direction: row;
  margin-top: 10px;
  justify-content: center;
  align-items: center;
  width: 120px;
  height: 40px;
  border: 1px solid #000000;
  padding: 0px 0px;
}

.sidecart-number-quantity .product-itemcount {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 18px;
  line-height: 85.8%;
  display: flex;
  color: #000000;
}

.itemcount-icon {
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 85.8%;
  display: flex;
  color: #000000;
}

.side-cart-price {
  width: 45px;
  height: 28px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 20px;
  line-height: 137.8%;
  color: #000000;
  margin-left: 350px;
}

.sidecart-line {
  width: 540px;
  height: 0px;
  margin-left: 40px;
  margin-top: 15px;
  margin-bottom: 15px;
  border: 1px solid #c7c7c7;
}

.sidecart-promo {
  margin-top: 200px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  width: 530px;
  margin-left: 55px;
}

.sidecart-details {
  margin-top: -30px;
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  width: 530px;
  margin-left: 55px;
}

.sidecart-details-gift {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
  width: 530px;
  margin-left: 55px;
  margin-bottom: 60px;
}

.sidecart-details-gift a {
  font-weight: bold;
  color: #000000;
  margin-top: 20px;
}

.sidecart-promo p {
  margin: 0px;
  width: 160px;
  height: 19px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 137.8%;
  color: #000000;
}

.sidecart-total {
  margin: 0px;
  width: 45px;
  height: 28px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  font-size: 20px;
  line-height: 137.8%;
  color: #000000;
}

.sidecart-total-amount {
  margin: 0px;
  width: 81px;
  height: 50px;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 137.8%;
  color: #000000;
  margin-top: 10px;
}

.sidecart-total {
  display: flex;
  width: 530px;
  justify-content: space-between;
  margin-left: 60px;
}